.accordion {
  position: relative;
}
.accordion__item {
  position: relative;
}
.accordion__item-head {
  position: relative;
  padding: 16px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  font-size: 18px;
  font-weight: 500;
  line-height: 26px;
  border-top: 1px solid #eaeaea;
  cursor: pointer;
}
@media only screen and (min-width: 1025px) {
  .accordion__item-head:hover svg {
    stroke: #ff3d00;
  }
}
.accordion__item.active .accordion__item-head svg {
  transform: scaleY(-1);
}
.accordion__item-head svg {
  stroke: #000;
  transition: 0.3s ease-in-out;
}
.accordion__item-body {
  position: relative;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 16px;
  overflow: hidden;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  white-space: normal;
  display: none;
}
@media only screen and (max-width: 767px) {
  .accordion__item-body {
    margin-left: -16px;
    margin-right: -16px;
    padding-left: 16px;
    padding-right: 16px;
  }
}
.accordion__item.full .accordion__item-body {
  -webkit-line-clamp: none !important;
}
.accordion__item.active .accordion__item-body {
  display: box;
  display: -webkit-box;
}
.accordion__item-body p {
  margin: 0 0 20px;
}
.accordion__item-body ul {
  padding: 0 0 0 25px;
  margin: 0 0 20px;
}
.accordion__item-body ul:last-child {
  margin: 0;
}
.accordion__item-body ul li {
  margin-bottom: 8px;
}
.accordion__item-body ul li span {
  font-weight: 500;
}
.accordion__item-body ul li:last-child {
  margin-bottom: 0;
}
.accordion__item-btn {
  position: relative;
  gap: 8px;
  align-items: center;
  justify-content: center;
  color: var(--Primary-Main, #01231f);
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
  text-decoration: underline;
  cursor: pointer;
  display: none;
  padding-bottom: 16px;
}
@media only screen and (min-width: 1025px) {
  .accordion__item-btn:hover {
    text-decoration: none;
  }
}
.accordion__item-btn svg {
  transition: 0.3s ease-in-out;
}
.accordion__item-btn span:nth-child(1) {
  display: block;
}
.accordion__item-btn span:nth-child(2) {
  display: none;
}
.accordion__item.full .accordion__item-btn svg {
  transform: scaleY(-1);
}
.accordion__item.full .accordion__item-btn span:nth-child(1) {
  display: none;
}
.accordion__item.full .accordion__item-btn span:nth-child(2) {
  display: block;
}
.accordion__item.active .accordion__item-btn {
  display: inline-flex;
}

