.author {
  position: relative;
  &__top {
    position: relative;
    background: var(--Secondary-Fire-Linear, linear-gradient(181deg, #01231f 54.86%, #ff3d00 90.11%));
    height: 100px;
    img {
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      opacity: 0.1;
    }
  }
  &__head {
    position: relative;
    display: flex;
    column-gap: 17px;
    padding-bottom: 32px;
    @include response(1200px) {
      display: block;
    }
    &::before {
      content: "";
      position: absolute;
      width: 100vw;
      height: 1px;
      background: #eaeaea;
      left: 50%;
      margin-left: -50vw;
      bottom: 0;
    }
  }
  &__ava {
    position: relative;
    flex: 0 0 132px;
    width: 132px;
    height: 132px;
    border-radius: 50%;
    overflow: hidden;
    margin-top: -40px;
    @include response(1200px) {
      margin-left: auto;
      margin-right: auto;
      width: 100px;
      height: 100px;
      margin-top: -50px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__head-box {
    padding-top: 24px;
    flex: 1 1 auto;
    overflow: auto;
    @include response(1200px) {
      padding-top: 8px;
    }
  }
  &__head-top {
    position: relative;
    padding-bottom: 16px;
    margin-bottom: 16px;
    border-bottom: 1px solid #eaeaea;
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 20px;
    @include response(1200px) {
      flex-direction: column;
    }
  }
  &__head-name {
    position: relative;
    font-family: "Instrument Sans";
    font-size: 28px;
    font-style: normal;
    font-weight: 500;
    line-height: 36px;
    @include response(1200px) {
      margin-bottom: 16px;
      text-align: center;
      font-size: 24px;
      line-height: 32px;
    }
  }
  &__social {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    column-gap: 8px;
    li {
      flex: 0 0 auto;
    }
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      width: 40px;
      height: 40px;
      border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
      transition: 0.2s ease-in-out;
      @include desktop-hover() {
        border-color: $red;
        transform: translateY(-2px);
        svg {
          fill: $red;
        }
      }
      svg {
        transition: 0.2s ease-in-out;
      }
    }
  }
  &__head-info {
    position: relative;
    padding-bottom: 16px;
    border-bottom: 1px solid #eaeaea;
    margin-bottom: 16px;
    display: flex;
    column-gap: 32px;
    @include response(1200px) {
      display: block;
    }
  }
  &__head-info-cell {
    position: relative;
    flex: 0 0 300px;
    width: 300px;
    @include response(1200px) {
      width: 100%;
      padding-bottom: 12px;
      margin-bottom: 12px;
      border-bottom: 1px solid #eaeaea;
      &:last-child {
        padding-bottom: 0;
        margin-bottom: 0;
        border-bottom: 0;
      }
    }
    &::before {
      content: "";
      position: absolute;
      background: #eaeaea;
      width: 1px;
      top: 0;
      bottom: 0;
      right: -16px;
    }
    &:last-child {
      &::before {
        display: none;
      }
    }
  }
  &__head-info-cell-title {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 8px;
    margin-bottom: 8px;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    svg {
      flex: 0 0 auto;
    }
  }
  &__head-info-cell-text {
    color: var(--Main-Gray, #595959);
    line-height: 20px;
  }
  &__head-text {
    position: relative;
  }
  &__head-text-inner {
    color: var(--Primary-Black, #000);
    font-size: 16px;
    line-height: 24px;
    max-height: 48px;
    overflow: hidden;
    -webkit-line-clamp: 2;
    display: box;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    margin-bottom: 10px;
    @include response(1200px) {
      -webkit-line-clamp: 4;
      max-height: 96px;
    }
    .js-show-text-parent.active & {
      -webkit-line-clamp: initial;
      max-height: none;
    }
    p {
      margin: 0 0 12px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__head-text-btn {
    display: inline-flex;
    align-items: center;
    font-weight: 500;
    gap: 8px;
    line-height: 24px;
    cursor: pointer;
    text-decoration: underline;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      text-decoration: none;
    }
    span {
      &:nth-child(2) {
        display: none;
      }
    }
    .js-show-text-parent.active & {
      svg {
        transform: scaleY(-1);
      }
      span {
        &:nth-child(1) {
          display: none;
        }
        &:nth-child(2) {
          display: block;
        }
      }
    }
  }
}
