.shipping {
  position: relative;
  &__top {
    position: relative;
    display: flex;
    padding-top: 60px;
    padding-bottom: 40px;
    column-gap: 16px;
    @include response(1200px) {
      padding-top: 32px;
      padding-bottom: 16px;
      display: block;
    }
  }
  &__top-info {
    position: relative;
    flex: 0 0 730px;
    width: 730px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    @include response(1200px) {
      width: 100%;
    }
  }
  &__top-title {
    margin-bottom: 24px;
  }
  &__top-text {
    line-height: 24px;
    margin-bottom: 24px;
    a {
      text-decoration: none;
      color: $red;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        color: $redH;
      }
    }
    p {
      margin: 0 0 24px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__nav {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    li {
      position: relative;
    }
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      column-gap: 8px;
      text-decoration: none;
      border-radius: 52px;
      border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
      background: var(--Primary-White, #fff);
      padding: 11px 15px;
      color: #000;
      line-height: 20px;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        border-color: $red;
        color: $red;
      }
    }
  }
  &__img {
    position: relative;
    margin-top: -60px;
    margin-bottom: -40px;
    flex: 0 0 auto;
    width: calc(50vw - 106px);
    @include response(1200px) {
      display: none;
    }
    img {
      width: 100%;
    }
  }
  &__body {
    position: relative;
  }
  &__row {
    position: relative;
    padding-top: 80px;
    padding-bottom: 80px;
    @include response(1200px) {
      padding-top: 32px;
      padding-bottom: 32px;
    }
    &:first-child {
      padding-top: 40px;
      @include response(1200px) {
        padding-top: 16px;
      }
    }
    &:last-child {
      padding-bottom: 40px;
      @include response(1200px) {
        padding-bottom: 0;
      }
    }
    &:nth-child(even) {
      &::before {
        content: "";
        position: absolute;
        background: #f5f5f5;
        width: 100vw;
        height: 100%;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        z-index: -1;
      }
    }
  }
  &__row-title {
    position: relative;
    margin-bottom: 32px;
    display: flex;
    align-items: center;
    column-gap: 16px;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
    @include response(1200px) {
      font-size: 22px;
      line-height: 30px;
      > img {
        width: 24px;
      }
    }
  }
  &__types {
    position: relative;
    margin-bottom: 24px;
    @include response(1200px) {
      margin-bottom: 16px;
    }
  }
  &__types-item {
    position: relative;
    display: flex;
    align-items: center;
    padding: 24px 64px 24px 32px;
    column-gap: 80px;
    border-radius: 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    margin-bottom: 24px;
    @include response(1200px) {
      padding: 16px;
      display: block;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__types-item-left {
    position: relative;
  }
  &__types-item-title {
    position: relative;
    display: flex;
    column-gap: 16px;
    margin-bottom: 16px;
    @include response(1200px) {
      column-gap: 12px;
    }
  }
  &__types-item-icon {
    position: relative;
    flex: 0 0 auto;
    display: flex;
    @include response(1200px) {
      flex: 0 0 24px;
      width: 24px;
    }
  }
  &__types-item-title-text {
    position: relative;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    padding-bottom: 12px;
    border-bottom: 1px solid #e6e9e9;
    flex: 1 1 auto;
  }
  &__types-item-info {
    position: relative;
    list-style: none;
    padding: 0 0 0 48px;
    margin: 0;
    @include response(1200px) {
      padding-left: 36px;
    }
    li {
      position: relative;
      display: flex;
      align-items: flex-start;
      margin-bottom: 16px;
      column-gap: 12px;
      font-size: 16px;
      line-height: 24px;
      @include response(1200px) {
        > img {
          display: none;
        }
      }
      span {
        font-weight: 500;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__types-item-right {
    position: relative;
    flex: 0 0 auto;
    @include response(1200px) {
      margin-top: 32px;
    }
  }
  &__attention {
    position: relative;
    border-radius: 8px;
    background: var(--Bg-Light-green, #ebf8f7);
    display: flex;
    padding: 16px 20px;
    column-gap: 12px;
    align-items: flex-start;
    max-width: 760px;
    width: 100%;
    svg {
      flex: 0 0 auto;
    }
  }
  &__attention-text {
    font-size: 16px;
    line-height: 24px;
    p {
      margin: 0;
    }
  }
  &__sliders {
    position: relative;
  }
  &__sliders-holder {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
    column-gap: 8px;
    @include response(1200px) {
      margin-bottom: 8px;
    }
  }
  &__sliders-btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    width: 32px;
    flex: 0 0 32px;
    height: 48px;
    border-radius: 4px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: var(--Primary-White, #fff);
    background: #fff;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      display: none;
    }
    @include desktop-hover() {
      border-color: $redH;
    }
    &.swiper-button-lock {
      display: none;
    }
    &.swiper-button-disabled {
      pointer-events: none;
      background: transparent;
      svg {
        stroke: #d3d3d3;
      }
    }
    svg {
      stroke: #000;
    }
  }
  &__slider-nav-holder {
    position: relative;
    flex: 1 1 auto;
  }
  &__slider-nav-select {
    display: none;
    @include response(1200px) {
      display: block;
      position: relative;
      border-radius: 8px;
      border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
      color: #000;
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      display: flex;
      align-items: center;
      column-gap: 10px;
      padding: 11px 15px;
      background: #fff;
      &::before {
        content: "";
        position: absolute;
        width: 9px;
        height: 9px;
        border-bottom: 1.5px solid #000;
        border-left: 1.5px solid #000;
        right: 16px;
        top: 50%;
        margin-top: -7px;
        transform: rotate(-45deg);
      }
    }
  }
  &__slider-nav {
    position: relative;
    @include response(1200px) {
      display: none;
      .shipping__slider-nav-holder.active & {
        display: block;
        position: absolute;
        top: calc(100% + 2px);
        left: 0;
        width: 100%;
        z-index: 2;
        overflow: visible;
      }
      .swiper-wrapper {
        display: block;
        background: #fff;
        padding: 8px;
        box-sizing: border-box;
        border-radius: 8px;
        box-shadow: 0px 6px 40px 0px rgba(0, 0, 0, 0.14);
        max-height: 345px;
        overflow-y: auto;
      }
    }
  }
  &__slider-nav-item {
    position: relative;
    border-radius: 8px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    color: var(--main-50, #8f8f8f);
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    display: flex;
    align-items: center;
    column-gap: 10px;
    padding: 7px 11px;
    cursor: pointer;
    width: auto;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      width: 100%;
      color: #000;
      border: 0;
      padding: 12px 8px;
      &.active {
        &::before {
          content: "";
          position: absolute;
          top: 50%;
          margin-top: -6px;
          right: 8px;
          width: 12px;
          height: 8px;
          border-bottom: 2px solid #0d9a88;
          border-left: 2px solid #0d9a88;
          transform: rotate(-45deg);
        }
      }
    }
    @include desktop-hover() {
      border-color: $red;
    }
    &.swiper-slide-thumb-active {
      background: var(--Primary-White, #fff);
    }
  }
  &__slider-for {
    position: relative;
  }
  &__slider-for-item {
    position: relative;
    border-radius: 8px;
    background: var(--Primary-White, #fff);
    padding: 40px;
    @include response(1200px) {
      padding: 16px;
      display: none;
      &.active {
        display: block;
      }
    }
  }
  &__slider-box {
    display: flex;
    column-gap: 64px;
    margin-bottom: 32px;
    @include response(1200px) {
      display: block;
      margin-bottom: 24px;
    }
  }
  &__slider-left {
    position: relative;
    @include response(1200px) {
      margin-bottom: 24px;
    }
  }
  &__slider-list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    gap: 12px;
    @include response(1200px) {
      display: block;
    }
  }
  &__slider-list-item {
    position: relative;
    border-radius: 8px;
    border: 1px solid #e5e5e5;
    background: var(--Primary-White, #fff);
    padding: 16px 24px;
    width: calc(50% - 6px);
    flex: 0 0 calc(50% - 6px);
    @include response(1200px) {
      width: 100%;
      margin-bottom: 12px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__slider-list-item-title {
    position: relative;
    margin-bottom: 8px;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__slider-list-item-row {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 8px;
    color: var(--Main-Gray, #595959);
    font-size: 16px;
    line-height: 24px;
  }
  &__slider-right {
    position: relative;
    flex: 0 0 376px;
    width: 376px;
    @include response(1200px) {
      width: 100%;
    }
  }
  &__slider-right-title {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 8px;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    margin-bottom: 12px;
  }
  &__slider-right-list {
    margin: 0;
    position: relative;
    padding: 0 0 0 25px;
    li {
      margin-bottom: 12px;
      font-size: 16px;
      line-height: 24px;
      font-weight: 500;
      span {
        font-weight: 400;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__tracking {
    position: relative;
    display: flex;
    justify-content: space-between;
    @include response(1200px) {
      display: block;
    }
  }
  &__tracking-cell {
    position: relative;
    flex: 0 0 640px;
    width: 640px;
    @include response(1200px) {
      width: 100%;
      margin-bottom: 32px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &:nth-child(2) {
      flex: 0 0 560px;
      width: 560px;
      @include response(1200px) {
        width: 100%;
      }
    }
  }
  &__tracking-title {
    position: relative;
    margin-bottom: 40px;
    display: flex;
    align-items: center;
    column-gap: 16px;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
    @include response(1200px) {
      margin-bottom: 24px;
      > img {
        width: 28px;
      }
    }
  }
  &__tracking-text {
    position: relative;
    font-size: 16px;
    line-height: 24px;
    p {
      margin: 0 0 24px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    ul {
      margin: 0 0 24px;
      padding: 0;
      list-style: none;
      &:last-child {
        margin-bottom: 0;
      }
      li {
        position: relative;
        display: flex;
        align-items: flex-start;
        column-gap: 12px;
        margin-bottom: 16px;
        &:last-child {
          margin-bottom: 0;
        }
        > img {
          flex: 0 0 auto;
          margin-top: 2px;
        }
        > span {
          > span {
            font-weight: 500;
          }
        }
      }
    }
  }
  &__tracking-info {
    position: relative;
    border-radius: 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    padding: 12px 16px;
    display: flex;
    align-items: flex-start;
    column-gap: 12px;
    margin-bottom: 24px;
    &:last-child {
      margin-bottom: 0;
    }
    > svg {
      flex: 0 0 auto;
    }
  }
}
