.home {
  position: relative;
  &__top {
    position: relative;
    background: var(--Bg-Light-gray, #f5f5f5);
    &::before {
      content: "";
      position: absolute;
      height: 130px;
      top: 0;
      left: 0;
      right: 0;
      opacity: 0.4;
      background: linear-gradient(180deg, #b3bdbc 0%, rgba(179, 189, 188, 0) 100%);
    }
  }
  &__top-box {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 50px;
    padding-top: 100px;
    padding-bottom: 100px;
    @include response(1200px) {
      padding-top: 30px;
      padding-bottom: 30px;
      column-gap: 0;
    }
  }
  &__top-left {
    position: relative;
    flex: 0 0 510px;
    width: 510px;
    @include response(1200px) {
      flex: 0 0 50%;
      width: 50%;
    }
    @include response(374px) {
      flex: 0 0 80%;
      width: 80%;
    }
  }
  &__top-date {
    position: relative;
    margin-bottom: 10px;
    display: inline-flex;
    border-radius: 8px;
    background: var(--Primary-White, #fff);
    padding: 4px 8px;
    align-items: center;
    column-gap: 8px;
    line-height: 20px;
    @include response(1200px) {
      font-size: 12px;
      line-height: 16px;
      padding-top: 0;
      padding-bottom: 0;
      column-gap: 4px;
      svg {
        width: 12px;
        height: 12px;
      }
    }
  }
  &__top-title {
    position: relative;
    font-family: "Instrument Sans";
    font-size: 40px;
    font-style: normal;
    font-weight: 500;
    line-height: 48px;
    margin: 0 0 24px;
    @include response(1200px) {
      font-size: 20px;
      line-height: 24px;
      margin-bottom: 12px;
    }
  }
  &__top-btn-box {
    position: relative;
    margin-bottom: 16px;
    @include response(1200px) {
      margin-bottom: 12px;
    }
  }
  &__top-btn {
    position: relative;
    @include response(1200px) {
      &.btn {
        font-size: 14px;
        line-height: 20px;
        padding: 4px 12px;
        > span {
          > span {
            display: none;
          }
        }
      }
    }
  }
  &__top-coupon {
    position: relative;
  }
  &__top-coupon-title {
    line-height: 20px;
    @include response(1200px) {
      font-size: 12px;
      line-height: 16px;
    }
  }
  &__top-coupon-val {
    color: var(--Primary-Accent, #ff3d00);
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    text-decoration: underline;
    @include response(1200px) {
      font-size: 14px;
      line-height: 20px;
    }
  }
  &__top-right {
    position: relative;
    margin-top: -100px;
    margin-bottom: -100px;
    flex: 0 0 calc(50vw + 105px);
    width: calc(50vw + 105px);
    align-self: stretch;
    @include response(1200px) {
      margin-top: -30px;
      margin-bottom: -30px;
      flex: 0 0 calc(50% + 16px);
      width: calc(50% + 16px);
    }
    @include response(374px) {
      flex: 0 0 calc(20% + 16px);
      width: calc(20% + 16px);
    }
  }
  &__top-img {
    display: block;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: left;
    }
  }
  &__real {
    position: relative;
    padding-top: 64px;
    padding-bottom: 32px;
    @include response(1200px) {
      padding-top: 32px;
      padding-bottom: 16px;
    }
    & + .home__slider {
      padding-top: 32px;
      @include response(1200px) {
        padding-top: 16px;
      }
    }
  }
  &__head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 24px;
  }
  &__title {
    position: relative;
  }
  &__all {
    flex: 0 0 auto;
  }
  &__real-list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    row-gap: 20px;
    margin-left: -20px;
    @include response(1200px) {
      row-gap: 8px;
    }
    @include response(767px) {
      margin-left: 0;
    }
  }
  &__real-item {
    position: relative;
    flex: 0 0 calc(33.333% - 20px);
    width: calc(33.333% - 20px);
    margin-left: 20px;
    overflow: hidden;
    height: 89px;
    padding: 24px;
    display: flex;
    align-items: center;
    border-radius: 4px;
    background: var(--Bg-Light-gray, #f5f5f5);
    color: var(--Primary-Black, #000);
    text-decoration: none;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      flex: 0 0 calc(50% - 20px);
      width: calc(50% - 20px);
    }
    @include response(767px) {
      height: 72px;
      margin-left: 0;
      flex: 0 0 100%;
      width: 100%;
    }
    @include desktop-hover() {
      color: $red;
      .home__real-decor {
        > img {
          height: 100%;
          &:nth-child(1) {
            opacity: 0;
          }
          &:nth-child(2) {
            opacity: 1;
          }
        }
      }
    }
  }
  &__real-decor {
    position: absolute;
    top: 0;
    height: 100%;
    right: 0;
    > img {
      height: 100%;
      transition: 0.3s ease-in-out;
      &:nth-child(2) {
        position: absolute;
        top: 0;
        right: 0;
        opacity: 0;
      }
    }
  }
  &__real-item-title {
    position: relative;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__real-item-img {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    > img {
      height: 100%;
      width: auto;
    }
  }
  &__slider {
    position: relative;
    padding-top: 64px;
    padding-bottom: 64px;
    & + .home__slider {
      padding-top: 0;
    }
    @include response(1200px) {
      padding-top: 32px;
      padding-bottom: 32px;
    }
  }
  &__slider-box {
    position: relative;
  }
  &__brands {
    position: relative;
    background: var(--Bg-Light-gray, #f5f5f5);
    padding-top: 32px;
    padding-bottom: 64px;
    @include response(1200px) {
      padding-bottom: 32px;
    }
  }
  &__brands-list {
    position: relative;
    display: flex;
    column-gap: 20px;
    @include response(1200px) {
      flex-wrap: wrap;
      column-gap: 30px;
      row-gap: 24px;
    }
  }
  &__brands-item {
    position: relative;
    width: 100%;
    text-decoration: none;
    color: var(--Primary-Black, #000);
    text-align: center;
    font-weight: 500;
    line-height: 20px;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      width: calc(33.333% - 20px);
    }
    @include desktop-hover() {
      color: #ff3d00;
      .home__brands-item-img {
        border-color: #ff3d00;
      }
    }
  }
  &__brands-item-img {
    position: relative;
    height: 104px;
    border-radius: 8px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: var(--Primary-White, #fff);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px;
    margin-bottom: 8px;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      height: 86px;
    }
    > img {
      max-height: 100%;
      width: auto;
    }
  }
  &__brands-item-title {
    position: relative;
  }
}
