.brands {
  position: relative;
  padding-top: 40px;
  @include response(1200px) {
    padding-top: 32px;
  }
  &__title {
    text-align: center;
    margin-bottom: 20px;
    @include response(1200px) {
      margin-bottom: 12px;
    }
  }
  &__description {
    position: relative;
    margin-bottom: 40px;
    max-width: 960px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    font-size: 16px;
    line-height: 24px;
    p {
      margin: 0;
    }
    @include response(1200px) {
      margin-bottom: 32px;
    }
  }
  &__list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    gap: 32px;
    margin-bottom: 60px;
    @include response(1200px) {
      margin-bottom: 32px;
      gap: 16px;
    }
  }
  &__item {
    position: relative;
    flex: 0 0 405px;
    width: 405px;
    border-radius: 8px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: var(--Primary-White, #fff);
    padding: 19px;
    @include response(1200px) {
      flex: 0 0 calc(50% - 8px);
      width: calc(50% - 8px);
    }
    @include response(767px) {
      flex: 0 0 100%;
      width: 100%;
    }
  }
  &__item-head {
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
    column-gap: 16px;
  }
  &__item-left {
    position: relative;
  }
  &__item-title {
    position: relative;
    margin-bottom: 10px;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__item-counter {
    position: relative;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    column-gap: 4px;
    line-height: 20px;
  }
  &__item-link {
    display: inline-block;
    vertical-align: top;
    color: $red;
    line-height: 20px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $redH;
      text-decoration: none;
    }
  }
  &__item-img {
    position: relative;
    display: flex;
    flex: 0 0 80px;
    height: 80px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__item-box {
    position: relative;
    border-radius: 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    border: 1px solid transparent;
    transition: .3s ease-in-out;
    .brands__item.active & {
      background: #fff;
      border-color: #bebebe;
      border-radius: 8px 8px 0 0;
    }
  }
  &__item-category {
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    column-gap: 8px;
    padding: 8px 16px;
    font-weight: 500;
    line-height: 20px;
    svg {
      flex: 0 0 auto;
    }
  }
  &__item-category-btn {
    position: relative;
    flex: 0 0 21px;
    width: 21px;
    height: 21px;
    margin-left: auto;
    &::before {
      content: "";
      position: absolute;
      width: 1px;
      height: 11px;
      background: #000;
      top: 5px;
      left: 10px;
      transition: .3s ease-in-out;
    }
    &::after {
      content: "";
      position: absolute;
      width: 11px;
      height: 1px;
      background: #000;
      top: 10px;
      left: 5px;
    }
    .brands__item.active & {
      &::before {
        transform: scaleY(0);
      }
    }
  }
  &__item-category-list {
    position: absolute;
    top: 100%;
    left: -1px;
    right: -1px;
    padding: 0 16px 8px;
    line-height: 20px;
    background: #fff;
    border-radius: 0 0 8px 8px;
    border: 1px solid #bebebe;
    border-top: 0;
    z-index: 1;
    display: none;
  }
  &__bottom {
    position: relative;
    background: #f5f5f5;
    margin-bottom: -40px;
    padding: 60px 16px;
    @include response(1200px) {
      padding-top: 32px;
      padding-bottom: 32px;
      margin-bottom: -20px;
    }
  }
  &__bottom-decor {
    position: absolute;
    bottom: 0;
    height: 100%;
    right: 0;
    @include response(1200px) {
      height: auto;
    }
  }
  &__bottom-box {
    position: relative;
    max-width: 1080px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include response(1200px) {
      flex-direction: column;
    }
  }
  &__bottom-left {
    position: relative;
    flex: 0 0 530px;
    width: 530px;
    @include response(1200px) {
      flex: 0 0 auto;
      width: 100%;
      margin-bottom: 32px;
      text-align: center;
    }
  }
  &__bottom-title {
    position: relative;
    margin-bottom: 24px;
    font-size: 28px;
    font-weight: 500;
    line-height: 36px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
  }
  &__bottom-text {
    font-size: 16px;
    line-height: 24px;
    p {
      margin: 0 0 24px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    a {
      color: $red;
      text-decoration: none;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        color: $redH;
      }
    }
  }
  &__bottom-right {
    position: relative;
    flex: 0 0 400px;
    width: 400px;
    @include response(1200px) {
      flex: 0 0 auto;
      width: 100%;
    }
  }
}
