.input-style {
  position: relative;
  margin-bottom: 24px;
  &:last-child {
    margin-bottom: 0;
  }
  &__label {
    position: relative;
    color: #595959;
    font-family: "Instrument Sans";
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    margin: 0 0 8px;
  }
  &__label-btn {
    color: $red;
    text-decoration: underline;
    cursor: pointer;
    @include desktop-hover() {
      text-decoration: none;
    }
  }
  &__input {
    width: 100%;
    border-radius: 8px;
    border: 1px solid #d3d3d3;
    background: #fff;
    padding: 12px 16px;
    color: var(--Primary-Black, #000);
    font-family: "Instrument Sans";
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: #bbb;
    }
    &:focus {
      border-color: #000;
    }
    &::placeholder {
      color: #969696;
      font-weight: 400;
    }
  }
  &__textarea {
    width: 100%;
    border-radius: 8px;
    border: 1px solid #d3d3d3;
    background: #fff;
    padding: 12px 16px;
    color: var(--Primary-Black, #000);
    font-family: "Instrument Sans";
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    field-sizing: content;
    min-height: 4lh;
    resize: none;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: #bbb;
    }
    &:focus {
      border-color: #000;
    }
    &::placeholder {
      color: #969696;
      font-weight: 400;
    }
  }
  &__show-password-parent {
    position: relative;
  }
  &__show-password {
    position: absolute;
    display: flex;
    top: 50%;
    right: 16px;
    margin-top: -8px;
    cursor: pointer;
    .input-style__show-password-parent.show & {
      svg {
        stroke: #000;
      }
      &::before {
        content: "";
        position: absolute;
        left: 7px;
        top: 0;
        width: 1px;
        height: 100%;
        background: #000;
        transform: rotate(45deg);
      }
    }
    @include desktop-hover() {
      svg {
        stroke: #000;
      }
    }
    svg {
      stroke: #bebebe;
      transition: 0.3s ease-in-out;
    }
  }
}

.checkbox-style {
  position: relative;
  display: block;
  margin-bottom: 12px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  @include desktop-hover() {
    color: $red;
  }
  span {
    display: block;
    position: relative;
    padding-left: 36px;
    line-height: 24px;
    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      border-radius: 4px;
      border: 1px solid var(--lines-light-gray-input, #cdcdcd);
      background: var(--background-bg-white-pages, #fff);
      width: 24px;
      height: 24px;
      transition: 0.3s ease-in-out;
    }
    &::after {
      content: "";
      position: absolute;
      left: 7px;
      top: 7px;
      width: 10px;
      height: 6px;
      border-bottom: 1px solid #fff;
      border-left: 1px solid #fff;
      transform: rotate(-45deg);
    }
  }
  input {
    display: none;
    &:checked {
      & + span {
        &::before {
          background: $red;
          border-color: $red;
        }
      }
    }
  }
  &:last-child {
    margin-bottom: 0;
  }
}

.add-input-btn {
  position: relative;
  margin-bottom: 24px;
  margin-top: -12px;
  display: inline-flex;
  vertical-align: top;
  cursor: pointer;
  gap: 8px;
  font-family: "Instrument Sans";
  font-weight: 500;
  line-height: 20px;
  color: $red;
  background: transparent;
  padding: 0;
  border: 0;
  transition: 0.3s ease-in-out;
  @include desktop-hover() {
    opacity: 0.8;
  }
}
