.checkout {
  position: relative;
  font-family: "Instrument Sans";
  overflow: hidden;
  &__box {
    display: flex;
    justify-content: center;
    min-height: 100dvh;
    gap: 80px;
    @include response(1200px) {
      flex-direction: column-reverse;
      margin-left: -16px;
      margin-right: -16px;
      gap: 0;
    }
  }
  &__left {
    position: relative;
    flex: 0 1 640px;
    padding: 32px 0;
    display: flex;
    flex-direction: column;
    @include response(1200px) {
      flex: 0 0 auto;
      padding: 16px;
    }
  }
  &__form {
    position: relative;
    flex: 1 1 auto;
  }
  &__right {
    position: relative;
    padding: 24px 0 24px 64px;
    flex: 0 1 544px;
    @include response(1200px) {
      flex: 0 0 auto;
      padding: 12px 16px;
      background: #01231f;
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      width: 100vw;
      background: var(--Primary-Main, #01231f);
      z-index: -1;
      @include response(1200px) {
        display: none;
      }
    }
  }
  &__steps {
    position: relative;
    padding-bottom: 24px;
    margin-bottom: 24px;
    border-bottom: 1px solid #eaeaea;
    display: flex;
    column-gap: 36px;
    @include response(1200px) {
      margin-bottom: 16px;
      padding-bottom: 16px;
      column-gap: 24px;
    }
  }
  &__steps-item {
    position: relative;
    display: flex;
    align-items: center;
    gap: 8px;
    @include response(1200px) {
      gap: 4px;
    }
    &::before {
      content: "";
      position: absolute;
      border: 4px solid transparent;
      border-left: 6px solid #bebebe;
      top: 50%;
      margin-top: -4px;
      right: -25px;
      @include response(1200px) {
        right: -20px;
      }
    }
    &:last-child {
      &::before {
        display: none;
      }
    }
  }
  &__steps-item-number {
    position: relative;
    width: 24px;
    flex: 0 0 24px;
    height: 24px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    text-align: center;
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
    .checkout__steps-item.active & {
      border-color: $red;
      background: $red;
      color: #fff;
    }
  }
  &__steps-item-title {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    .checkout__steps-item.active & {
      color: $red;
    }
    @include response(767px) {
      font-size: 12px;
    }
    @include response(666px) {
      font-size: 10px;
    }
    @include response(370px) {
      font-size: 8px;
    }
  }
  &__section {
    position: relative;
  }
  &__title {
    position: relative;
    margin-bottom: 24px;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
  }
  &__input {
    position: relative;
    margin-bottom: 24px;
  }
  &__input-row {
    position: relative;
    display: flex;
    gap: 16px;
    @include response(767px) {
      display: block;
    }
  }
  &__input-cell {
    position: relative;
    width: calc(50% - 8px);
    flex: 0 0 calc(50% - 8px);
    .checkout__input-row--full & {
      width: calc(33.333% - 10.6px);
      flex: 0 0 calc(33.333% - 10.6px);
      @include response(767px) {
        width: 100%;
        flex: 0 0 100%;
      }
    }
    @include response(767px) {
      width: 100%;
      flex: 0 0 100%;
    }
  }
  &__triggers {
    position: relative;
    margin-bottom: 24px;
    display: flex;
    justify-content: center;
    padding: 12px;
    border-radius: 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    gap: 16px;
    @include response(767px) {
      padding: 8px;
    }
  }
  &__triggers-item {
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    gap: 8px;
    justify-content: center;
    @include response(767px) {
      flex-direction: column;
      gap: 4px;
      text-align: center;
    }
    &::before {
      content: "";
      position: absolute;
      height: 24px;
      width: 1px;
      background: #eaeaea;
      top: 50%;
      margin-top: -12px;
      right: -8px;
      @include response(767px) {
        top: 0;
        margin-top: 0;
        height: 100%;
      }
    }
    &:last-child {
      &::before {
        display: none;
      }
    }
  }
  &__triggers-icon {
    position: relative;
    display: flex;
    flex: 0 0 24px;
  }
  &__triggers-title {
    line-height: 20px;
  }
  &__form-controls {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include response(767px) {
      flex-direction: column-reverse;
    }
    &--end {
      justify-content: flex-end;
    }
  }
  &__form-back {
    position: relative;
    text-decoration: none;
    display: flex;
    align-items: center;
    gap: 8px;
    color: var(--Primary-Black, #000);
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    transition: 0.3s ease-in-out;
    @include response(767px) {
      margin-top: 16px;
    }
    @include desktop-hover() {
      color: $red;
      svg {
        stroke: $red;
      }
    }
    svg {
      stroke: #01231f;
      transition: 0.3s ease-in-out;
    }
  }
  &__form-btn {
    @include response(1200px) {
      width: 100%;
    }
  }
  &__copy {
    color: var(--main-50, #8f8f8f);
    line-height: 20px;
    padding-top: 24px;
    margin-top: 24px;
    border-top: 1px solid #eaeaea;
    @include response(1200px) {
      text-align: center;
    }
  }
  &__logo-box {
    position: relative;
    margin-bottom: 24px;
    @include response(1200px) {
      margin-bottom: 16px;
      text-align: center;
    }
  }
  &__logo {
    display: inline-flex;
    vertical-align: top;
  }
  &__list {
    position: relative;
    padding-top: 20px;
    padding-bottom: 20px;
    border-top: 1px solid rgba(#d3d3d3, 0.1);
    @include response(1200px) {
      display: none;
      .checkout__right.active & {
        display: block;
      }
    }
  }
  &__item {
    position: relative;
    margin-bottom: 20px;
    display: flex;
    gap: 16px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__item-img {
    position: relative;
    display: flex;
    flex: 0 0 80px;
    width: 80px;
    height: 80px;
    border-radius: 4px;
    background: var(--Bg-Light-gray, #f5f5f5);
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__item-box {
    position: relative;
    flex: 1 1 auto;
  }
  &__item-name {
    position: relative;
    display: block;
    text-decoration: none;
    color: #fff;
    margin-bottom: 12px;
    font-size: 16px;
    line-height: 24px;
    @include desktop-hover() {
      text-decoration: underline;
    }
  }
  &__item-price {
    position: relative;
    flex: 0 0 auto;
    color: var(--Primary-White, #fff);
    font-size: 18px;
    font-weight: 500;
    line-height: 24px;
    @include response(1200px) {
      font-size: 16px;
      font-weight: 700;
    }
  }
  &__promo {
    position: relative;
    padding-top: 20px;
    margin-bottom: 20px;
    border-top: 1px solid rgba(#d3d3d3, 0.1);
    @include response(1200px) {
      display: none;
      .checkout__right.active & {
        display: block;
      }
    }
  }
  &__promo-form {
    position: relative;
    display: flex;
    gap: 12px;
  }
  &__promo-btn {
    flex: 0 0 auto;
  }
  &__promo-expired {
    position: relative;
    color: #ec4454;
    text-align: center;
    line-height: 20px;
    border-radius: 4px;
    background: #582e2e;
    margin-top: 12px;
    padding: 4px;
  }
  &__promo-value {
    position: relative;
    display: inline-flex;
    vertical-align: top;
    margin-top: 12px;
    align-items: center;
    padding: 7px 11px;
    gap: 4px;
    border-radius: 52px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: var(--Primary-White, #fff);
    cursor: pointer;
    line-height: 20px;
    transition: 0.3s ease-in-out;
    span {
      font-weight: 500;
      color: $red;
    }
    @include desktop-hover() {
      border-color: $red;
      svg {
        stroke: $red;
      }
    }
  }
  &__promo-value-remove {
    display: flex;
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
  }
  &__price {
    position: relative;
    padding-top: 20px;
    border-top: 1px solid rgba(#d3d3d3, 0.1);
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
    @include response(1200px) {
      display: none;
      .checkout__right.active & {
        display: block;
      }
    }
  }
  &__price-row {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 12px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__price-cell {
    position: relative;
    color: var(--Primary-White, #fff);
    line-height: 20px;
    &:last-child {
      font-size: 18px;
      font-weight: 500;
      line-height: 26px;
      @include response(1200px) {
        font-size: 16px;
        font-weight: 700;
      }
    }
    &--total {
      font-size: 18px;
      font-weight: 500;
      line-height: 26px;
      &:last-child {
        font-size: 22px;
        font-weight: 500;
        line-height: 30px;
      }
    }
  }
  &__errors {
    position: relative;
    border-radius: 8px;
    background: #ffe6e6;
    margin-bottom: 24px;
    display: flex;
    align-items: flex-start;
    gap: 14px;
    padding: 14px;
  }
  &__errors-icon {
    position: relative;
    display: flex;
    flex: 0 0 auto;
  }
  &__errors-box {
    position: relative;
  }
  &__errors-text {
    position: relative;
    color: var(--Secondary-Red, #f70808);
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 12px;
    &:last-child {
      margin-bottom: 0;
    }
    p {
      margin: 0;
    }
  }
  &__errors-list {
    margin: 0 0 12px;
    padding: 0;
    list-style: none;
    position: relative;
    border-bottom: 1px solid rgba(#f70808, 0.12);
    li {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: space-between;
      color: var(--Secondary-Red, #f70808);
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      padding-top: 12px;
      padding-bottom: 12px;
      border-top: 1px solid rgba(#f70808, 0.12);
      span {
        display: flex;
        cursor: pointer;
        @include desktop-hover() {
          svg {
            stroke: #000;
          }
        }
        svg {
          stroke: #f70808;
          transition: 0.3s ease-in-out;
        }
      }
    }
  }
  &__logged {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 12px;
    gap: 12px;
    border-radius: 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    margin-bottom: 32px;
  }
  &__logged-img {
    position: relative;
    flex: 0 0 32px;
    width: 32px;
    height: 32px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__logged-box {
    position: relative;
    flex: 1 1 auto;
  }
  &__logged-title {
    position: relative;
    color: var(--main-50, #8f8f8f);
    line-height: 20px;
    margin-bottom: 2px;
  }
  &__logged-name {
    color: var(--Main-Gray, #595959);
    font-weight: 500;
    line-height: 20px;
  }
  &__logged-btn {
    position: relative;
    text-decoration: none;
    color: var(--Primary-Black, #000);
    font-weight: 500;
    line-height: 20px;
    flex: 0 0 auto;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $red;
    }
  }
  &__user {
    position: relative;
    border-radius: 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    padding: 16px;
    margin-bottom: 24px;
  }
  &__user-row {
    position: relative;
    display: flex;
    align-items: flex-start;
    padding-bottom: 16px;
    margin-bottom: 16px;
    border-bottom: 1px solid #d3d3d3;
    &:last-child {
      border-bottom: 0;
      padding-bottom: 0;
      margin-bottom: 0;
    }
  }
  &__user-title {
    position: relative;
    flex: 0 0 80px;
    width: 80px;
    color: var(--Main-Gray, #595959);
    font-size: 16px;
    line-height: 24px;
    padding-right: 10px;
  }
  &__user-contact {
    position: relative;
    flex: 1 1 auto;
    color: var(--Primary-Black, #000);
    font-family: "Instrument Sans";
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    font-style: normal;
  }
  &__user-edit {
    position: relative;
    text-decoration: none;
    align-self: stretch;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 4px;
    flex: 0 0 auto;
    @include desktop-hover() {
      svg {
        stroke: $red;
      }
    }
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
  }
  &__radio-list {
    position: relative;
    margin-bottom: 24px;
  }
  &__radio-item {
    position: relative;
    &:first-child {
      .checkout__radio-label {
        > span {
          border-radius: 8px 8px 0 0;
        }
      }
    }
    &:last-child {
      .checkout__radio-label {
        > span {
          border-radius: 0 0 8px 8px;
        }
      }
    }
    & + .checkout__radio-item {
      margin-top: -1px;
      .checkout__radio-label {
        input {
          &:checked {
            & + span {
              border-top-color: $red;
            }
          }
        }
      }
    }
  }
  &__radio-label {
    position: relative;
    display: block;
    input {
      display: none;
      &:checked {
        & + span {
          background: #fff3ef;
          border-color: $red;
          z-index: 1;
          &::before {
            background: $red;
            border-color: $red;
          }
          &::after {
            opacity: 1;
          }
        }
      }
    }
    > span {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 16px;
      padding: 16px 16px 16px 56px;
      border: 1px solid #cdcdcd;
      background: #fff;
      font-family: "Instrument Sans";
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      cursor: pointer;
      transition: 0.3s ease-in-out;
      @include response(375px) {
        font-size: 14px;
      }
      &::before {
        content: "";
        position: absolute;
        width: 24px;
        height: 24px;
        border-radius: 50%;
        border: 1px solid #cdcdcd;
        background: var(--Bg-Light-gray, #f5f5f5);
        top: 50%;
        margin-top: -12px;
        left: 16px;
        transition: 0.3s ease-in-out;
      }
      &::after {
        content: "";
        position: absolute;
        width: 12px;
        height: 12px;
        background: #fff;
        border-radius: 50%;
        top: 50%;
        margin-top: -6px;
        left: 22px;
        opacity: 0;
        transition: 0.3s ease-in-out;
      }
      > span {
        display: flex;
        align-items: center;
        gap: 4px;
        @include response(767px) {
          > img {
            max-width: 30px;
          }
        }
      }
    }
  }
  &__balance {
    position: relative;
    margin-bottom: 24px;
    padding: 16px;
    border-radius: 8px;
    border: 1px solid var(--Line-Light, #eaeaea);
    background: var(--Primary-White, #fff);
    display: flex;
    align-items: center;
    gap: 16px;
  }
  &__balance-icon {
    position: relative;
    flex: 0 0 auto;
    display: flex;
  }
  &__balance-box {
    position: relative;
    flex: 1 1 auto;
  }
  &__balance-title {
    color: var(--Main-Gray, #595959);
    font-size: 16px;
    line-height: 24px;
    @include response(1200px) {
      font-size: 15px;
    }
  }
  &__balance-value {
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__balance-checkbox {
    position: relative;
    flex: 0 0 auto;
  }
  &__text {
    position: relative;
    margin-bottom: 24px;
    color: var(--Main-Gray, #595959);
    line-height: 24px;
    p {
      margin: 0;
    }
    a {
      color: var(--Main-Gray, #595959);
      @include desktop-hover() {
        text-decoration: none;
      }
    }
  }
  &__agreement {
    margin-bottom: 24px;
  }
  &__radio-body {
    display: none;
    background: var(--Bg-Light-gray, #f5f5f5);
    padding: 24px 16px;
  }
  &__radio-body-row {
    display: flex;
    column-gap: 16px;
    @include response(1200px) {
      display: block;
    }
    .input-style {
      width: 100%;
    }
  }
  &__order {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 24px;
    column-gap: 14px;
    @include response(1200px) {
      margin-bottom: 16px;
    }
  }
  &__order-icon {
    display: flex;
  }
  &__order-box {
    position: relative;
  }
  &__order-value {
    position: relative;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__order-text {
    color: var(--Main-Gray, #595959);
    font-size: 16px;
  }
  &__map {
    position: relative;
    border-radius: 8px;
    border: 1px solid var(--Line-Light, #eaeaea);
    overflow: hidden;
    margin-bottom: 24px;
    @include response(1200px) {
      margin-bottom: 16px;
    }
  }
  &__map-progress {
    position: relative;
    padding-top: 12px;
    padding-bottom: 8px;
    display: flex;
    justify-content: center;
    column-gap: 32px;
    @include response(1200px) {
      column-gap: 16px;
      padding-top: 8px;
    }
  }
  &__map-progress-item {
    position: relative;
    width: 100%;
    text-align: center;
    padding-left: 4px;
    padding-right: 4px;
    &::before {
      content: "";
      position: absolute;
      top: 11px;
      left: 58%;
      width: calc(91% + 16px);
      height: 2px;
      background: #e6e9e9;
      @include response(1200px) {
        left: 64%;
        width: calc(80% + 8px);
      }
    }
    &:last-child {
      &::before {
        display: none;
      }
    }
  }
  &__map-progress-icon {
    display: flex;
    justify-content: center;
    margin-bottom: 2px;
    svg {
      fill: #4d6562;
    }
    .active.checkout__map-progress-item & {
      svg {
        fill: #0d9a88;
      }
    }
  }
  &__map-progress-status {
    position: relative;
    font-weight: 500;
    line-height: 20px;
  }
  &__map-progress-date {
    position: relative;
    color: var(--Main-Gray, #595959);
    font-size: 12px;
    line-height: 16px;
    opacity: 0;
    .active.checkout__map-progress-item & {
      opacity: 1;
    }
  }
  &__map-canvas {
    position: relative;
    iframe {
      vertical-align: top;
      @include response(767px) {
        height: 126px;
      }
    }
  }
  &__info {
    position: relative;
    padding: 16px;
    border-radius: 8px;
    border: 1px solid var(--Line-Light, #eaeaea);
    margin-bottom: 24px;
  }
  &__info-title {
    position: relative;
    margin-bottom: 12px;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__info-text {
    position: relative;
    color: var(--Main-Gray, #595959);
    line-height: 20px;
    p {
      margin: 0 0 12px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    a {
      color: $red;
      @include desktop-hover() {
        text-decoration: none;
      }
    }
  }
  &__details {
    position: relative;
    display: flex;
    column-gap: 14px;
  }
  &__details-col {
    position: relative;
    max-width: calc(50% - 7px);
  }
  &__details-item {
    position: relative;
    margin-bottom: 21px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__details-title {
    position: relative;
    color: var(--Main-Gray, #595959);
    line-height: 20px;
    margin-bottom: 5px;
  }
  &__details-value {
    font-size: 16px;
    line-height: 24px;
    word-wrap: break-word;
    img {
      margin-right: 5px;
      vertical-align: middle;
    }
  }
  &__order-mobile-btn {
    display: none;
    @include response(1200px) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: relative;
      column-gap: 8px;
      > svg {
        flex: 0 0 auto;
      }
      .checkout__right.active & {
        margin-bottom: 16px;
      }
    }
  }
  &__order-mobile-text {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 8px;
    color: var(--Primary-White, #fff);
    font-weight: 500;
    line-height: 20px;
    @include response(374px) {
      font-size: 9px;
    }
    span {
      &:nth-child(2) {
        display: none;
      }
    }
    .checkout__right.active & {
      svg {
        transform: scaleY(-1);
      }
    }
  }
  &__order-mobile-total {
    position: relative;
    flex: 0 0 auto;
    margin-left: auto;
    color: var(--Primary-White, #fff);
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
}
