.individual {
  position: relative;
  &__top-holder {
    position: relative;
    overflow: hidden;
  }
  &__top {
    position: relative;
    display: flex;
    column-gap: 80px;
    @include response(1200px) {
      display: block;
    }
  }
  &__left {
    position: relative;
    flex: 0 0 560px;
    width: 560px;
    padding-bottom: 56px;
    @include response(1200px) {
      width: 100%;
      padding-bottom: 32px;
    }
  }
  &__title {
    margin-bottom: 24px;
    @include response(1200px) {
      margin-bottom: 16px;
    }
  }
  &__triggers {
    position: relative;
    margin-bottom: 24px;
    display: flex;
    border-radius: 8px;
    border: 1px solid var(--Line-Light, #eaeaea);
    background: var(--Primary-White, #fff);
    column-gap: 24px;
    padding: 12px;
    @include response(1200px) {
      padding: 16px 0 0;
      border: 0;
    }
    @include response(666px) {
      column-gap: 8px;
    }
  }
  &__triggers-item {
    width: 100%;
    position: relative;
    text-align: center;
    &:last-child {
      &::before {
        display: none;
      }
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      width: 1px;
      background: #eaeaea;
      right: -12px;
      @include response(666px) {
        right: -4px;
      }
    }
  }
  &__triggers-head {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 2px;
    column-gap: 8px;
    color: var(--Main-Gray, #595959);
    line-height: 20px;
    @include response(1200px) {
      font-size: 12px;
      line-height: 16px;
    }
    @include response(666px) {
      font-size: 9px;
    }
  }
  &__triggers-text {
    position: relative;
    color: var(--Primary-Black, #000);
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    span {
      cursor: pointer;
      text-decoration: underline;
      @include desktop-hover() {
        text-decoration: none;
      }
    }
    @include response(1200px) {
      font-size: 14px;
      line-height: 20px;
    }
  }
  &__blockquote {
    position: relative;
    display: block;
    margin: 0 0 24px;
    color: var(--Main-Gray, #595959);
    font-family: "Instrument Sans";
    font-size: 18px;
    font-style: italic;
    font-weight: 400;
    line-height: 24px;
  }
  &__video {
    position: relative;
    margin-bottom: 24px;
  }
  &__video-title {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__video-link {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 8px;
    a {
      color: var(--Primary-Black, #000);
      line-height: 20px;
      @include desktop-hover() {
        text-decoration: none;
      }
    }
  }
  &__controls {
    position: relative;
    display: flex;
    column-gap: 12px;
    @include response(666px) {
      display: block;
    }
  }
  &__controls-btn {
    width: 100%;
    @include response(666px) {
      margin-bottom: 12px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__right {
    position: relative;
    flex: 0 0 auto;
    @include response(1200px) {
      margin-left: -16px;
      margin-right: -16px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__nav {
    position: sticky;
    top: 0;
    border-bottom: 1px solid var(--Line-Light, #eaeaea);
    background: var(--Bg-Light-gray, #f5f5f5);
    padding-top: 12px;
    padding-bottom: 12px;
    z-index: 1;
  }
  &__nav-list {
    position: relative;
    display: flex;
    list-style: none;
    padding: 0 16px;
    margin: 0 -16px;
    column-gap: 12px;
    overflow-x: auto;
    li {
      position: relative;
      flex: 0 0 auto;
    }
    a {
      display: block;
      text-decoration: none;
      border-radius: 52px;
      border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
      background: var(--Primary-White, #fff);
      color: var(--Primary-Black, #000);
      line-height: 20px;
      padding: 8px 12px;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        border-color: $red;
        color: $red;
      }
    }
  }
  &__box {
    position: relative;
    padding-top: 64px;
    padding-bottom: 64px;
    border-bottom: 1px solid #eaeaea;
    @include response(1200px) {
      padding-top: 32px;
      padding-bottom: 32px;
    }
    &:last-child {
      border-bottom: 0;
      padding-bottom: 0;
    }
  }
  &__small-title {
    position: relative;
    margin-bottom: 32px;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
  }
  &__use-list {
    position: relative;
    display: flex;
    column-gap: 40px;
    @include response(1200px) {
      column-gap: 16px;
      margin-left: -16px;
      margin-right: -16px;
      padding-left: 16px;
      padding-right: 16px;
      overflow-x: auto;
    }
  }
  &__use-item {
    position: relative;
    @include response(1200px) {
      flex: 0 0 90%;
      width: 90%;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__info {
    position: relative;
    display: flex;
    column-gap: 40px;
    @include response(1200px) {
      display: block;
    }
  }
  &__info-left {
    position: relative;
    flex: 0 0 480px;
    width: 480px;
    @include response(1200px) {
      width: 100%;
    }
  }
  &__info-list {
    position: relative;
  }
  &__info-item {
    position: relative;
    padding-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #eaeaea;
    &:first-child {
      padding-top: 0;
    }
    &:last-child {
      padding-bottom: 0;
      border-bottom: 0;
    }
  }
  &__info-item-title {
    position: relative;
    margin-bottom: 2px;
    color: var(--Main-Gray, #595959);
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
  }
  &__info-item-text {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__info-right {
    position: relative;
    flex: 0 0 760px;
    width: 760px;
    @include response(1200px) {
      width: 100%;
      margin-bottom: 24px;
    }
  }
  &__info-imgs {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
    @include response(1200px) {
      gap: 8px;
    }
  }
  &__info-img {
    position: relative;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &--half {
      position: relative;
      flex: 0 0 calc(50% - 8px);
      width: calc(50% - 8px);
      @include response(1200px) {
        flex: 0 0 calc(50% - 4px);
        width: calc(50% - 4px);
      }
    }
    &--big {
      flex: 0 0 446px;
      width: 446px;
      @include response(1200px) {
        flex: 0 0 calc(64% - 4px);
        width: calc(64% - 4px);
      }
    }
    &--small {
      flex: 0 0 296px;
      width: 296px;
      @include response(1200px) {
        flex: 0 0 calc(36% - 4px);
        width: calc(36% - 4px);
      }
    }
    &--full {
      flex: 0 0 100%;
      width: 100%;
    }
  }
  &__brand {
    position: relative;
    margin-bottom: 24px;
    display: flex;
    align-items: center;
    column-gap: 8px;
  }
  &__brand-icon {
    display: flex;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__brand-title {
    color: var(--Primary-Black, #000);
    opacity: 0.8;
    font-weight: 500;
    line-height: 20px;
  }
  &__brand-btn {
    line-height: 20px;
    text-decoration: underline;
    cursor: pointer;
    @include desktop-hover() {
      text-decoration: none;
    }
  }
}
