.contact {
  position: relative;
  padding-top: 40px;
  @include response(1200px) {
    padding-top: 16px;
  }
  &__title {
    margin-bottom: 40px;
    text-align: center;
    @include response(1200px) {
      margin-bottom: 32px;
    }
  }
  &__nav {
    position: relative;
    display: flex;
    justify-content: center;
    margin-bottom: 32px;
    @include response(767px) {
      flex: 0 0 auto;
      overflow-x: auto;
      margin-left: -16px;
      margin-right: -16px;
      padding-left: 16px;
      padding-right: 16px;
      justify-content: flex-start;
    }
  }
  &__tabs-list {
    position: relative;
    flex: 0 0 auto;
  }
  &__options {
    position: relative;
    max-width: 480px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
  &__form {
    position: relative;
  }
  &__form-row {
    position: relative;
    margin-bottom: 20px;
  }
  &__form-checkbox {
    position: relative;
    margin-bottom: 20px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: 24px;
    @include response(767px) {
      white-space: initial;
    }
    a {
      color: #000;
      @include desktop-hover() {
        text-decoration: none;
      }
    }
  }
  &__form-checkbox-label {
    position: relative;
    display: inline-block;
    vertical-align: top;
    cursor: pointer;
    @include desktop-hover() {
      span {
        &::before {
          border-color: $red;
        }
      }
    }
    input {
      display: none;
      &:checked {
        & + span {
          &::before {
            background: $red;
            border-color: $red;
          }
        }
      }
    }
    span {
      position: relative;
      display: inline-block;
      vertical-align: top;
      padding-left: 32px;
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        margin-top: -12px;
        width: 24px;
        height: 24px;
        border-radius: 4px;
        border: 1px solid var(--lines-light-gray-input, #cdcdcd);
        background: var(--background-bg-white-pages, #fff);
        transition: 0.3s ease-in-out;
      }
      &::after {
        content: "";
        position: absolute;
        width: 10px;
        height: 6px;
        border-bottom: 1px solid #fff;
        border-left: 1px solid #fff;
        top: 50%;
        margin-top: -5px;
        left: 7px;
        transform: rotate(-45deg);
      }
    }
  }
  &__form-btn {
    width: 100%;
  }
  &__form-captcha {
    position: relative;
    margin-bottom: 20px;
  }
}
