.brand {
  position: relative;
  &__triggers {
    position: relative;
    background: var(--main-70, #4d6562);
    padding-top: 48px;
    padding-bottom: 48px;
    @include response(1200px) {
      padding-top: 32px;
      padding-bottom: 32px;
    }
  }
  &__triggers-list {
    position: relative;
    display: flex;
    justify-content: center;
    column-gap: 96px;
    @include response(1200px) {
      display: block;
    }
  }
  &__triggers-item {
    position: relative;
    width: 100%;
    @include response(1200px) {
      padding-top: 16px;
      padding-bottom: 16px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.16);
      &:last-child {
        padding-bottom: 0;
        border-bottom: 0;
      }
      &:first-child {
        padding-top: 0;
      }
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      background: rgba(255, 255, 255, 0.16);
      width: 1px;
      right: -48px;
      @include response(1200px) {
        display: none;
      }
    }
    &:last-child {
      &::before {
        display: none;
      }
    }
  }
  &__triggers-head {
    position: relative;
    margin-bottom: 12px;
    display: flex;
    align-items: center;
    column-gap: 12px;
  }
  &__triggers-icon {
    position: relative;
    flex: 0 0 36px;
    width: 36px;
  }
  &__triggers-title {
    color: var(--Primary-White, #fff);
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__triggers-text {
    color: var(--Primary-White, #fff);
    font-size: 16px;
    line-height: 155%;
  }
  &__archive {
    position: relative;
    padding-top: 40px;
    padding-bottom: 64px;
    overflow: hidden;
  }
  &__archive-title {
    margin-bottom: 32px;
    text-align: center;
    @include response(1200px) {
      margin-bottom: 24px;
    }
  }
  &__archive-slider {
    margin-bottom: 32px;
    max-width: 628px;
    width: 100%;
    &.swiper {
      overflow: visible;
    }
  }
  &__archive-btn-box {
    text-align: center;
  }
  &__archive-btn {
    position: relative;
  }
  &__archive-pagination {
    padding-top: 32px;
    display: flex;
    justify-content: center;
  }
  &__top {
    position: relative;
    background: var(--Bg-Light-gray, #f5f5f5);
    padding-top: 80px;
    padding-bottom: 70px;
    @include response(1200px) {
      padding-top: 32px;
      padding-bottom: 24px;
    }
  }
  &__top-container {
    @include response(1200px) {
      display: flex;
      flex-direction: column;
    }
  }
  &__top-decor {
    position: absolute;
    right: 0;
    top: 65px;
    @include response(1200px) {
      margin-left: -50px;
      margin-right: -50px;
      max-width: calc(100% + 100px);
      top: 50%;
      transform: translateY(-50%);
    }
  }
  &__top-title {
    text-align: center;
    position: relative;
    margin: 0 0 16px;
    padding-bottom: 16px;
    text-align: center;
    font-size: 40px;
    font-weight: 500;
    line-height: 48px;
    @include response(1200px) {
      font-size: 28px;
      line-height: 34px;
      margin-bottom: 12px;
      padding-bottom: 12px;
    }
    &::before {
      content: "";
      position: absolute;
      width: 620px;
      height: 1px;
      background: #d8d8d8;
      bottom: 0;
      left: 50%;
      margin-left: -310px;
      @include response(1200px) {
        width: 100%;
        left: 0;
        margin-left: 0;
      }
    }
  }
  &__top-text {
    position: relative;
    text-align: center;
    margin-bottom: 24px;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    @include response(1200px) {
      color: var(--Main-Gray, #595959);
      font-weight: 400;
      margin-bottom: 10px;
    }
  }
  &__top-btn-box {
    position: relative;
    text-align: center;
    @include response(1200px) {
      order: 4;
    }
  }
  &__top-btn {
    z-index: 1;
  }
  &__top-img {
    position: relative;
    margin-top: -48px;
    @include response(1200px) {
      margin-top: 0;
      margin-left: -16px;
      margin-right: -16px;
      margin-bottom: 24px;
    }
    img {
      width: 100%;
    }
  }
  &__options {
    position: relative;
    padding-top: 80px;
    padding-bottom: 40px;
    @include response(1200px) {
      padding-top: 32px;
      padding-bottom: 32px;
    }
  }
  &__options-title {
    position: relative;
    text-align: center;
    font-size: 28px;
    font-weight: 500;
    line-height: 36px;
    margin-bottom: 20px;
    @include response(1200px) {
      font-size: 24px;
      line-height: 32px;
      margin-bottom: 12px;
    }
  }
  &__options-text {
    position: relative;
    margin-bottom: 40px;
    margin-left: auto;
    margin-right: auto;
    max-width: 960px;
    width: 100%;
    color: var(--Main-Gray, #595959);
    text-align: center;
    font-size: 16px;
    line-height: 24px;
    @include response(1200px) {
      margin-bottom: 24px;
    }
  }
  &__options-list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    column-gap: 24px;
    row-gap: 40px;
    @include response(1200px) {
      row-gap: 24px;
    }
  }
  &__option {
    position: relative;
    border-radius: 8px;
    border: 1px solid #d3d3d3;
    background: #fff;
    padding: 40px 32px 0 32px;
    flex: 0 0 calc(33.333% - 16px);
    width: calc(33.333% - 16px);
    @include response(1200px) {
      flex: 0 0 calc(50% - 12px);
      width: calc(50% - 12px);
      padding-left: 24px;
      padding-right: 24px;
    }
    @include response(767px) {
      flex: 0 0 100%;
      width: 100%;
    }
    &--coupon {
      border-top-right-radius: 0;
      border-top-left-radius: 0;
      @include response(767px) {
        border-top-right-radius: 8px;
        border-top-left-radius: 8px;
      }
    }
    &--full {
      flex: 0 0 100%;
      width: 100%;
      display: flex;
      align-items: center;
      column-gap: 24px;
      padding-bottom: 68px;
      @include response(1200px) {
        flex: 0 0 calc(50% - 12px);
        width: calc(50% - 12px);
        display: block;
        padding-bottom: 0;
      }
      @include response(767px) {
        flex: 0 0 100%;
        width: 100%;
      }
    }
  }
  &__option-label {
    position: absolute;
    top: -1px;
    right: -1px;
    padding: 6px 10px 6px 12px;
    border-radius: 0 8px 0 24px;
    display: flex;
    align-items: center;
    column-gap: 4px;
    color: var(--Primary-White, #fff);
    font-weight: 500;
    line-height: 20px;
    .brand__option--coupon & {
      border-top-right-radius: 0;
      @include response(767px) {
        border-top-right-radius: 8px;
      }
    }
  }
  &__option-head {
    position: relative;
    margin-bottom: 24px;
    @include response(1200px) {
      margin-bottom: 16px;
    }
    .brand__option--full & {
      margin-bottom: 0;
      flex: 0 0 460px;
      width: 460px;
      @include response(1200px) {
        width: 100%;
        margin-bottom: 16px;
      }
    }
  }
  &__option-top {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    column-gap: 16px;
  }
  &__option-img {
    position: relative;
    width: 64px;
    height: 64px;
    flex: 0 0 64px;
    border-radius: 50%;
    overflow: hidden;
    @include response(1200px) {
      width: 48px;
      height: 48px;
      flex: 0 0 48px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__option-top-box {
    position: relative;
  }
  &__option-title {
    position: relative;
    font-size: 22px;
    font-weight: 500;
    line-height: 30px;
    margin-bottom: 4px;
    @include response(1200px) {
      font-size: 18px;
      line-height: 26px;
    }
  }
  &__option-rate {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 8px;
  }
  &__option-rate-value {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__option-rate-stars {
    position: relative;
  }
  &__option-rate-reviews {
    line-height: 20px;
  }
  &__option-text {
    position: relative;
    color: var(--Main-Gray, #595959);
    font-size: 16px;
    line-height: 24px;
  }
  &__option-body {
    position: relative;
    margin-bottom: 24px;
    @include response(1200px) {
      margin-bottom: 16px;
    }
    .brand__option--full & {
      flex: 0 0 362px;
      width: 362px;
      margin-bottom: 0;
      @include response(1200px) {
        width: 100%;
        margin-bottom: 16px;
      }
    }
  }
  &__option-row {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 12px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__option-row-icon {
    position: relative;
    margin-right: 8px;
    flex: 0 0 20px;
    width: 20px;
  }
  &__option-row-title {
    position: relative;
    color: var(--Main-Gray, #595959);
    font-size: 16px;
    line-height: 24px;
    margin-right: 4px;
    @include response(374px) {
      font-size: 12px;
    }
  }
  &__option-row-val {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    @include response(374px) {
      font-size: 12px;
    }
  }
  &__option-footer {
    .brand__option--full & {
      flex: 1 1 auto;
    }
  }
  &__option-coupon {
    position: absolute;
    bottom: calc(100% + 1px);
    left: -1px;
    right: -1px;
    border-radius: 8px 8px 0px 0px;
    background: var(--Bg-Light-red, #ffe4e4);
    padding: 2px 4px;
    color: var(--Primary-Black, #000);
    font-weight: 500;
    line-height: 20px;
    text-align: center;
    @include response(767px) {
      position: relative;
      bottom: auto;
      right: auto;
      left: auto;
      margin-left: 7px;
      margin-right: 7px;
      color: var(--Secondary-Red, #f70808);
      font-size: 12px;
      font-weight: 700;
      line-height: 16px;
    }
    span {
      color: $red;
      font-weight: 700;
    }
  }
  &__option-btn {
    margin-bottom: 16px;
    width: 100%;
  }
  &__option-pay {
    position: relative;
    margin-bottom: 24px;
    display: flex;
    align-items: center;
    column-gap: 4px;
    @include response(666px) {
      font-size: 12px;
    }
    @include response(374px) {
      font-size: 10px;
    }
    .brand__option--full & {
      margin-bottom: 0;
      @include response(1200px) {
        margin-bottom: 24px;
      }
    }
  }
  &__option-pay-text {
    line-height: 20px;
  }
  &__option-pay-img {
    display: flex;
  }
  &__option-pay-link {
    position: relative;
    line-height: 20px;
    color: #000;
    @include desktop-hover() {
      text-decoration: none;
    }
  }
  &__option-more {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 8px;
    color: var(--Primary-Black, #000);
    font-weight: 500;
    line-height: 20px;
    padding: 8px 24px;
    border-radius: 0px 0px 8px 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    margin-left: -32px;
    margin-right: -32px;
    text-decoration: none;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      margin-left: -24px;
      margin-right: -24px;
    }
    .brand__option--full & {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      margin-left: 0;
      margin-right: 0;
      border-top: 1px solid #d3d3d3;
      @include response(1200px) {
        position: relative;
        bottom: auto;
        left: auto;
        right: auto;
        border-top: 0;
        margin-left: -24px;
        margin-right: -24px;
      }
    }
    @include desktop-hover() {
      color: $red;
      svg {
        transform: translateX(4px);
        stroke: $red;
      }
    }
    svg {
      fill: none;
      stroke: #01231f;
      transition: 0.3s ease-in-out;
    }
  }
  &__reviews {
    position: relative;
    background: var(--Bg-Light-gray, #f5f5f5);
    padding-top: 60px;
    padding-bottom: 60px;
    @include response(1200px) {
      padding-top: 32px;
      padding-bottom: 32px;
    }
  }
  &__reviews-title {
    text-align: center;
    margin-bottom: 40px;
    @include response(1200px) {
      margin-bottom: 24px;
    }
  }
  &__reviews-box {
    position: relative;
    display: flex;
    align-items: center;
    @include response(1200px) {
      flex-direction: column;
    }
  }
  &__reviews-img {
    position: relative;
    flex: 0 0 280px;
    width: 280px;
    @include response(1200px) {
      width: 100%;
      text-align: center;
      margin-bottom: 16px;
      flex: 0 0 auto;
    }
    img {
      width: 100%;
      @include response(1200px) {
        width: auto;
      }
    }
  }
  &__reviews-slider-box {
    position: relative;
    flex: 1 1 auto;
    padding-left: 48px;
    padding-right: 48px;
    overflow: auto;
    @include response(1200px) {
      flex: 0 0 auto;
      max-width: 100%;
    }
  }
  &__reviews-slider {
    position: relative;
    @include response(767px) {
      max-width: 228px;
      width: 100%;
    }
  }
  &__reviews-slider-item {
    position: relative;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__reviews-slider-btn {
    position: absolute;
    top: 50%;
    margin-top: -16px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    cursor: pointer;
    z-index: 1;
    @include desktop-hover() {
      svg {
        stroke: $red;
      }
    }
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
    &--prev {
      left: 0;
    }
    &--next {
      right: 0;
    }
    &.swiper-button-disabled {
      opacity: 0;
      visibility: hidden;
    }
  }
  &__testimonials {
    position: relative;
    padding-top: 64px;
    padding-bottom: 24px;
    overflow: hidden;
    @include response(1200px) {
      padding-top: 32px;
      padding-bottom: 12px;
    }
  }
  &__testimonials-title {
    text-align: center;
    margin-bottom: 32px;
    @include response(1200px) {
      margin-bottom: 24px;
    }
  }
  &__testimonials-slider-box {
    position: relative;
    max-width: 980px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
  &__testimonials-slider {
    &.swiper {
      overflow: visible;
    }
  }
  &__testimonials-item {
    position: relative;
    border-radius: 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    padding: 24px;
    display: flex;
    align-items: center;
    column-gap: 32px;
    @include response(1200px) {
      display: block;
    }
  }
  &__testimonials-video {
    position: relative;
    flex: 0 0 450px;
    width: 450px;
    border-radius: 8px;
    overflow: hidden;
    @include response(1200px) {
      display: block;
      margin-bottom: 16px;
      width: 100%;
    }
    @include desktop-hover() {
      &::before {
        border-color: $red;
        background: $red;
      }
    }
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -32px;
      margin-left: -32px;
      width: 64px;
      height: 64px;
      border-radius: 50%;
      border: 1px solid var(--Line-Opacity---16, rgba(255, 255, 255, 0.16));
      background: var(--Line-Opacity---16, rgba(255, 255, 255, 0.16));
      backdrop-filter: blur(7px);
      transition: 0.3s ease-in-out;
    }
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -8px;
      margin-left: -4px;
      border: 8px solid transparent;
      border-left: 12px solid #fff;
      border-right: 0;
      z-index: 1;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__testimonials-item-box {
    position: relative;
  }
  &__testimonials-item-icon {
    position: relative;
    margin-bottom: 16px;
  }
  &__testimonials-item-quotes {
    position: relative;
    margin-bottom: 32px;
    color: var(--Main-Gray, #595959);
    font-size: 18px;
    font-style: italic;
    line-height: 24px;
    @include response(1200px) {
      margin-bottom: 16px;
    }
  }
  &__testimonials-item-author {
    position: relative;
  }
  &__testimonials-item-author-name {
    position: relative;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__testimonials-item-author-channel {
    line-height: 20px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    column-gap: 4px;
    @include response(1200px) {
      span {
        width: 100%;
        margin-bottom: 4px;
      }
    }
  }
  &__testimonials-item-author-link {
    color: #000;
    @include desktop-hover() {
      text-decoration: none;
    }
  }
  &__testimonials-slider-btn {
    position: absolute;
    top: 50%;
    margin-top: -16px;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    cursor: pointer;
    @include desktop-hover() {
      svg {
        stroke: $red;
      }
    }
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
    &--prev {
      left: -56px;
    }
    &--next {
      right: -56px;
    }
  }
  &__testimonials-slider-pagination {
    display: flex;
    justify-content: center;
    padding-top: 32px;
  }
}
