.text-content {
  position: relative;
  color: var(--Primary-Black, #000);
  font-family: "Instrument Sans";
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  @include response(1200px) {
    font-size: 17px;
  }
  p {
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  ol {
    list-style: none;
    padding: 0;
    counter-reset: myCounter;
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    li {
      margin-bottom: 12px;
      position: relative;
      padding-left: 40px;
      &:last-child {
        margin-bottom: 0;
      }
      &::before {
        counter-increment: myCounter;
        content: counter(myCounter) ".";
        position: absolute;
        left: 12px;
        top: 0;
      }
    }
  }
  ul:not(.mobile-table-content__list) {
    list-style: none;
    padding: 0;
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    li {
      margin-bottom: 12px;
      position: relative;
      padding-left: 32px;
      &::before {
        content: "";
        position: absolute;
        left: 12px;
        top: 10px;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background: #4d6562;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  iframe {
    border: 0;
    max-width: 100%;
    vertical-align: top;
    margin: 0 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    @include response(767px) {
      width: 100%;
      height: 300px;
    }
  }
  h1 {
    color: var(--Primary-Black, #000);
    font-family: "Instrument Sans";
    font-size: 28px;
    font-style: normal;
    font-weight: 500;
    line-height: 32px;
    margin: 0 0 20px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    @include response(767px) {
      font-size: 26px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h2 {
    color: var(--Primary-Black, #000);
    font-family: "Instrument Sans";
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 32px;
    margin: 0 0 20px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    @include response(767px) {
      font-size: 22px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h3 {
    color: var(--Primary-Black, #000);
    font-family: "Instrument Sans";
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 36px;
    margin: 0 0 20px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    @include response(767px) {
      font-size: 20px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h4 {
    font-family: "Instrument Sans";
    color: var(--Primary-Black, #000);
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 36px;
    margin: 0 0 20px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h5 {
    color: var(--Primary-Black, #000);
    font-family: "Instrument Sans";
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    margin: 0 0 20px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  img:not(.decor-list__icon, .looking__img, .looking__decor) {
    display: inline-block;
    vertical-align: top;
    width: 100%;
    border-radius: 2px;
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  a {
    color: #000;
    @include desktop-hover() {
      text-decoration: none;
    }
  }
  table {
    min-width: 800px;
    border-collapse: collapse;
    &:last-child {
      margin-bottom: 0;
    }
    thead {
      border-bottom: 1px solid var(--Line-Light, #eaeaea);
      tr {
        td {
          font-weight: 500;
        }
      }
    }
    tbody {
      tr {
        &:nth-child(odd) {
          background: #f5f5f5;
          td {
            &:first-child {
              background: #f5f5f5;
            }
          }
        }
      }
    }
    tr {
      border-bottom: 1px solid var(--Line-Light, #eaeaea);
      &:last-child {
        border-bottom: 0;
      }
    }
    td {
      padding: 8px 12px;
      border-right: 1px solid var(--Line-Light, #eaeaea);
      &:last-child {
        border-right: 0;
      }
      &:first-child {
        position: sticky;
        background: #fff;
        left: 0;
        z-index: 2;
      }
    }
  }
}

.info-1 {
  position: relative;
  border-left: 2px solid var(--Secondary-Approval-Green, #0d9a88);
  background: #f1f9f8;
  padding: 8px 10px;
  display: flex;
  align-items: center;
  column-gap: 6px;
  margin-bottom: 12px;
  font-size: 13px;
  color: #0d9a88;
  &--orange {
    color: var(--Colors-Orange, var(--Colors-Orange, #ff9500));
    border-left-color: #ff9500;
    background: #f9f3eb;
  }
  > div {
    position: relative;
  }
  svg {
    flex: 0 0 20px;
    width: 20px;
  }
  font-weight: 500;
  line-height: 20px;
  span {
    color: var(--Main-Gray, #595959);
    font-weight: 400;
    a {
      color: #000;
      @include desktop-hover() {
        text-decoration: none;
      }
    }
  }
}

.decor-list {
  position: relative;
  padding: 24px;
  margin-bottom: 20px;
  &__icon {
    position: absolute;
    top: 0;
    left: 0;
  }
  &__title {
    position: relative;
    margin-bottom: 8px;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
}

.looking {
  position: relative;
  border-radius: 8px;
  background: var(--Bg-Light-gray, #f5f5f5);
  padding: 18px 20px;
  overflow: hidden;
  margin-bottom: 40px;
  &__decor {
    position: absolute;
    bottom: 0;
    right: 0;
  }
  &__head {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 12px;
    margin-bottom: 12px;
    padding-bottom: 12px;
    border-bottom: 1px solid #e6e9e9;
    @include response(1200px) {
      display: block;
    }
  }
  &__title {
    position: relative;
    font-size: 22px;
    font-weight: 500;
    line-height: 30px;
    flex: 1 1 auto;
    @include response(1200px) {
      margin-bottom: 12px;
    }
  }
  &__imgs {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 16px;
    flex: 0 0 auto;
    @include response(1200px) {
      padding-left: 20px;
      padding-right: 20px;
      margin-left: -20px;
      margin-right: -20px;
      overflow-x: auto;
    }
  }
  &__img {
    position: relative;
  }
  &__body {
    position: relative;
  }
  &__text {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 4px;
  }
  &__description {
    position: relative;
    margin-bottom: 12px;
    color: var(--Primary-Accent, #ff3d00);
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__btn {
    position: relative;
  }
}

.summary {
  position: relative;
  border-left: 2px solid var(--Primary-Accent, #ff3d00);
  background: var(--Bg-Light-gray, #f5f5f5);
  padding: 16px 24px;
  margin-bottom: 20px;
  &__title {
    color: var(--Primary-Accent, #ff3d00);
    font-weight: 500;
    margin-bottom: 8px;
  }
  p {
    margin: 0;
  }
}

.table-wrapper {
  border: 1px solid var(--Line-Light, #eaeaea);
  overflow-x: auto;
  max-width: 100%;
  margin: 0 0 20px;
}

.attention {
  position: relative;
  border-radius: 4px;
  border-left: 2px solid var(--main-50, #8f8f8f);
  background: var(--Bg-Light-gray, #f5f5f5);
  padding: 16px;
  display: flex;
  column-gap: 12px;
  margin-bottom: 20px;
  &--red {
    border-left-color: var(--Colors-Red, #ff3b30);
    background: rgba(246, 11, 11, 0.06);
  }
  &--green {
    border-left-color: var(--Secondary-Approval-Green, #0d9a88);
    background: rgba(13, 154, 136, 0.06);
  }
  svg {
    flex: 0 0 24px;
    width: 24px;
  }
  p {
    margin: 0;
  }
}

.experts {
  position: relative;
  margin-top: 20px;
  margin-bottom: 40px;
  padding-top: 20px;
  border-top: 1px solid #eaeaea;
  &__title {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 8px;
    margin-bottom: 8px;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__text {
    margin-bottom: 8px;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
  }
  &__list {
    position: relative;
    display: flex;
    column-gap: 12px;
    @include response(767px) {
      display: block;
    }
  }
  &__item {
    position: relative;
    width: 100%;
    padding: 12px;
    border-radius: 12px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    @include response(767px) {
      margin-bottom: 12px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__item-head {
    position: relative;
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    column-gap: 18px;
  }
  &__item-ava {
    position: relative;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background: var(--secondary-10, #f5f2ef);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
  }
  &__item-box {
    position: relative;
  }
  &__item-title {
    position: relative;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__item-social {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    column-gap: 2px;
    li {
      position: relative;
    }
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 26px;
      height: 26px;
      transition: 0.2s ease-in-out;
      @include desktop-hover() {
        transform: translateY(-2px);
      }
    }
  }
  &__item-text {
    line-height: 20px;
  }
}

.sources {
  position: relative;
  margin-bottom: 40px;
  &__item {
    position: relative;
    border-radius: 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    border: 1px solid transparent;
    margin-bottom: 20px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: #8f8f8f;
    }
    &.active {
      border-color: #8f8f8f;
      background: #fff;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__item-head {
    position: relative;
    cursor: pointer;
    padding: 16px 60px 16px 24px;
    color: var(--Primary-Black, #000);
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 26px;
    &::before {
      content: "";
      position: absolute;
      width: 13px;
      height: 1px;
      background: #000;
      top: 50%;
      right: 30px;
    }
    &::after {
      content: "";
      position: absolute;
      width: 1px;
      height: 13px;
      background: #000;
      top: 50%;
      margin-top: -6px;
      right: 36px;
      transition: 0.3s ease-in-out;
    }
    .sources__item.active & {
      &::after {
        transform: scaleY(0);
      }
    }
  }
  &__item-body {
    position: relative;
    padding: 0 24px 16px;
    color: var(--Primary-Black, #000);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    display: none;
    p {
      margin: 0 0 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    ol {
      margin: 0;
      padding: 0 0 0 20px;
      li {
        position: relative;
        span {
          color: #0d9a88;
        }
        a {
          color: #ff3d00;
          text-decoration: none;
          transition: 0.3s ease-in-out;
          @include desktop-hover() {
            color: $redH;
          }
        }
      }
    }
  }
}

.next {
  position: relative;
  margin-top: 40px;
  @include response(1200px) {
    margin-bottom: 40px;
  }
  &__title {
    color: var(--Primary-Black, #000);
    margin: 0 0 24px;
    font-family: "Instrument Sans";
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 32px;
  }
  &__list {
    position: relative;
  }
  &__item {
    position: relative;
    display: flex;
    align-items: center;
    background: var(--Bg-Light-gray, #f5f5f5);
    border-radius: 8px;
    overflow: hidden;
    margin-bottom: 24px;
    @include response(1200px) {
      border-radius: 0;
      background: #fff;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__item-img {
    position: relative;
    flex: 0 0 256px;
    width: 256px;
    align-self: stretch;
    background: #000;
    @include response(1200px) {
      flex: 0 0 156px;
      width: 156px;
      border-radius: 4px;
      overflow: hidden;
    }
    @include desktop-hover() {
      img {
        opacity: 0.9;
      }
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.3s ease-in-out;
    }
  }
  &__item-info {
    position: relative;
    padding: 16px 24px;
    @include response(1200px) {
      padding: 0 0 0 12px;
      overflow: auto;
    }
  }
  &__item-labels {
    position: relative;
    margin: 0 0 12px;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    gap: 10px 28px;
    @include response(1200px) {
      margin-bottom: 8px;
      flex-wrap: nowrap;
      column-gap: 20px;
    }
    li {
      flex: 0 0 auto;
      position: relative;
      &:last-child {
        &::before {
          display: none;
        }
      }
      &::before {
        content: "";
        position: absolute;
        width: 4px;
        height: 4px;
        border-radius: 50%;
        background: #ff3d00;
        top: 50%;
        margin-top: -2px;
        right: -16px;
        @include response(1200px) {
          right: -12px;
        }
      }
    }
    a {
      display: block;
      color: var(--Primary-Accent, #ff3d00);
      font-weight: 500;
      line-height: 20px;
      text-decoration: none;
      transition: 0.3s ease-in-out;
      @include response(1200px) {
        font-size: 12px;
        font-style: normal;
        font-weight: 500;
        line-height: 16px;
      }
      @include desktop-hover() {
        color: $redH;
      }
    }
  }
  &__item-title {
    display: block;
    position: relative;
    font-family: "Instrument Sans";
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 26px;
    margin: 0 0 12px;
    color: #000;
    text-decoration: none;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $red;
    }
    @include response(1200px) {
      overflow: hidden;
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      max-height: 48px;
      -webkit-line-clamp: 2;
      display: box;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      margin-bottom: 8px;
    }
  }
  &__item-footer {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 16px;
  }
  &__item-author {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 12px;
  }
  &__item-author-img {
    position: relative;
    width: 32px;
    height: 32px;
    flex: 0 0 32px;
    border-radius: 50%;
    overflow: hidden;
    @include response(767px) {
      display: none;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__item-author-name {
    font-weight: 500;
    line-height: 20px;
    color: #000;
    text-decoration: none;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $red;
    }
    @include response(1200px) {
      font-weight: 400;
    }
  }
  &__item-date {
    color: var(--Main-Gray, #595959);
    @include response(767px) {
      display: none;
    }
  }
}

.mobile-table-content {
  display: none;
  @include response(1200px) {
    display: block;
    position: relative;
    border-radius: 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    padding: 4px 16px;
    &.active {
      .mobile-table-content__list {
        display: block;
      }
      .mobile-table-content__title {
        svg {
          transform: scaleY(1);
        }
      }
    }
  }
  &__title {
    color: var(--Primary-Black, #000);
    font-family: "Instrument Sans";
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    padding-top: 8px;
    padding-bottom: 8px;
    position: relative;
    svg {
      position: absolute;
      top: 50%;
      right: 0;
      margin-top: -12px;
      transform: scaleY(-1);
      transition: 0.3s ease-in-out;
    }
  }
  &__list {
    border-top: 1px solid #eaeaea;
    position: relative;
    list-style: none;
    padding: 8px 0 0;
    margin: 0;
    display: none;
    li {
      position: relative;
      padding: 0;
    }
    a {
      position: relative;
      display: inline-block;
      vertical-align: top;
      padding: 8px 0;
      text-decoration: none;
      color: var(--Primary-Black, #000);
      font-family: "Instrument Sans";
      font-size: 17px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;
      &::before {
        content: "";
        position: absolute;
        bottom: 6px;
        left: 0;
        width: 100%;
        border-bottom: 1px dashed #4d6562;
      }
    }
  }
}
