.faq {
  position: relative;
  padding-top: 32px;
  &__box {
    position: relative;
    display: flex;
    align-items: flex-start;
    column-gap: 60px;
    padding-right: 80px;
    @include response(1200px) {
      padding-right: 0;
      display: block;
    }
  }
  &__nav {
    position: sticky;
    top: 10px;
    flex: 0 0 240px;
    width: 240px;
    @include response(1200px) {
      position: relative;
      top: auto;
      margin-left: -16px;
      margin-right: -16px;
      margin-bottom: 32px;
      padding-left: 16px;
      padding-right: 16px;
      width: calc(100% + 32px);
      overflow-x: auto;
      display: flex;
    }
  }
  &__nav-list {
    position: relative;
    list-style: none;
    padding: 4px;
    margin: 0;
    border-radius: 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    @include response(1200px) {
      display: flex;
      flex: 0 0 auto;
    }
    li {
      position: relative;
      margin-bottom: 8px;
      @include response(1200px) {
        flex: 0 0 auto;
        margin-bottom: 0;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
    div {
      position: relative;
      cursor: pointer;
      border-radius: 4px;
      padding: 8px 16px;
      color: var(--main-50, #8f8f8f);
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      overflow: hidden;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.04);
        background: var(--Primary-White, #fff);
        color: #000;
      }
      &.active {
        box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.04);
        background: var(--Primary-White, #fff);
        color: #000;
        &::before {
          opacity: 1;
        }
      }
      &::before {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        width: 2px;
        background: $red;
        opacity: 0;
        transition: 0.3s ease-in-out;
      }
    }
  }
  &__info {
    position: relative;
    flex: 1 1 auto;
  }
  &__info-title {
    position: relative;
    @include response(1200px) {
      margin-bottom: 32px;
      text-align: center;
    }
  }
  &__section {
    position: relative;
    padding-top: 40px;
    @include response(1200px) {
      padding-top: 12px;
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__section-head {
    position: relative;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    column-gap: 16px;
  }
  &__section-img {
    position: relative;
    display: flex;
    flex: 0 0 auto;
    @include response(1200px) {
      flex: 0 0 24px;
      width: 24px;
      height: 24px;
    }
  }
  &__section-title {
    position: relative;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
    @include response(1200px) {
      font-size: 18px;
    }
  }
  &__section-list {
    position: relative;
  }
  &__section-item {
    position: relative;
    border-radius: 8px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    transition: 0.3s ease-in-out;
    margin-bottom: 12px;
    &:last-child {
      margin-bottom: 0;
    }
    @include desktop-hover() {
      border-color: #8f8f8f;
    }
    &.active {
      border-color: #8f8f8f;
    }
  }
  &__section-item-head {
    position: relative;
    padding: 16px 24px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 16px;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    @include response(1200px) {
      padding: 12px 16px;
      font-size: 16px;
      line-height: 24px;
    }
    .faq__section-item.active & {
      svg {
        transform: scaleY(-1);
      }
    }
  }
  &__section-item-body {
    position: relative;
    padding: 0 24px 16px;
    font-size: 16px;
    line-height: 24px;
    display: none;
    @include response(1200px) {
      padding-left: 16px;
      padding-right: 16px;
    }
    p {
      margin: 0 0 16px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
