.portfolio {
  position: relative;
  padding-top: 48px;
  @include response(1200px) {
    padding-top: 32px;
  }
  &__title {
    margin-bottom: 32px;
    @include response(1200px) {
      margin-bottom: 24px;
    }
  }
  &__nav {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0 0 32px;
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    @include response(1200px) {
      flex-wrap: nowrap;
      margin-left: -16px;
      margin-right: -16px;
      padding-left: 16px;
      padding-right: 16px;
      overflow-x: auto;
      margin-bottom: 24px;
    }
    li {
      flex: 0 0 auto;
    }
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      text-decoration: none;
      column-gap: 8px;
      color: var(--Primary-Black, #000);
      font-family: "Instrument Sans";
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;
      padding: 3px 12px;
      border-radius: 52px;
      border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
      background: var(--Primary-White, #fff);
      height: 100%;
      transition: 0.3s ease-in-out;
      &.active {
        background: #fff3ef;
        border-color: $red;
      }
      @include desktop-hover() {
        border-color: $red;
      }
      span {
        width: 32px;
        height: 32px;
        border-radius: 50%;
        overflow: hidden;
        margin-left: -8px;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
  }
  &__list {
    position: relative;
    margin-bottom: 32px;
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
    @include response(1200px) {
      gap: 16px;
    }
  }
  &__item {
    display: block;
    position: relative;
    text-decoration: none;
    border-radius: 8px;
    overflow: hidden;
    .portfolio__list & {
      flex: 0 0 calc(50% - 12px);
      width: calc(50% - 12px);
    }
    @include response(1023px) {
      .portfolio__list & {
        flex: 0 0 100%;
        width: 100%;
      }
    }
    @include desktop-hover() {
      .portfolio__item-box {
        &::before {
          height: 120%;
        }
      }
    }
    > img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__item-box {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    align-items: flex-end;
    column-gap: 16px;
    padding: 80px 24px 24px;
    @include response(1200px) {
      padding: 80px 16px 16px;
    }
    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(1, 35, 31, 0) 0%, rgba(1, 35, 31, 0.8) 100%);
      transition: 0.3s ease-in-out;
    }
  }
  &__item-left {
    position: relative;
    flex: 1 1 auto;
    overflow: auto;
  }
  &__item-head {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 4px;
    column-gap: 8px;
    color: rgba(255, 255, 255, 0.8);
    font-weight: 500;
    line-height: 20px;
  }
  &__item-icon {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    flex: 0 0 32px;
    background: #fff;
    border-radius: 50%;
  }
  &__item-title {
    display: block;
    position: relative;
    color: var(--Primary-White, #fff);
    font-size: 22px;
    font-weight: 500;
    line-height: 30px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    @include response(1200px) {
      font-size: 18px;
      line-height: 26px;
      white-space: normal;
      max-height: 52px;
      overflow: hidden;
      -webkit-line-clamp: 2;
      display: box;
      display: -webkit-box;
      -webkit-box-orient: vertical;
    }
  }
  &__item-right {
    position: relative;
    padding: 4px 8px 4px 24px;
    border-left: 1px solid var(--Line-Opacity---16, rgba(255, 255, 255, 0.16));
    flex: 0 0 auto;
  }
  &__item-price {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 4px;
    color: rgba(255, 255, 255, 0.8);
    line-height: 20px;
    svg {
      opacity: 0.8;
    }
  }
  &__item-price-value {
    position: relative;
    color: var(--Primary-White, #fff);
    font-size: 22px;
    font-weight: 500;
    line-height: 30px;
    @include response(1200px) {
      font-size: 18px;
      line-height: 26px;
    }
  }
  &__bottom {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__bottom-text {
    position: relative;
    margin-bottom: 16px;
    color: var(--Main-Gray, #595959);
    text-align: center;
    line-height: 20px;
  }
  &__more {
    position: relative;
    min-width: 218px;
  }
}
