.select2-container .select2-selection--single {
  height: 48px;
}

.select-small .select2-container .select2-selection--single {
  height: 36px;
}

.select2-container .select2-selection--single .select2-selection__rendered {
  padding-left: 16px;
  padding-right: 48px;
  @include response(767px) {
    padding-right: 30px;
  }
}

.select2-dropdown {
  border-radius: 8px;
  background: #fff;
  box-shadow: 0px 6px 40px 0px rgba(0, 0, 0, 0.14);
  padding: 8px;
  border: 0;
}

.select2-results__option {
  padding: 12px 16px;
  border-radius: 8px;
  transition: 0.3s ease-in-out;
  &--disabled {
    background: #f5f5f5;
  }
}

.select2-container--default {
  .select2-selection--single {
    border-radius: 8px;
    border: 1px solid #d3d3d3;
    transition: border-color 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: #4a4a4a;
    }
  }
  &.select2-container--focus {
    .select2-selection--single {
      border-color: #4a4a4a;
    }
  }
}

.select2-container--default .select2-selection--single .select2-selection__rendered {
  color: var(--Primary-Black, #000);
  font-size: 16px;
  font-weight: 500;
  line-height: 46px;
}
.select-small .select2-container--default .select2-selection--single .select2-selection__rendered {
  line-height: 34px;
  font-size: 14px;
}

.select2-container--default .select2-selection--single .select2-selection__placeholder {
  color: #848484;
  font-weight: 400;
}

.select2-container--default .select2-selection--single .select2-selection__arrow {
  width: 16px;
  height: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 50%;
  margin-top: -10px;
  right: 16px;
  @include response(767px) {
    right: 12px;
  }
  &::before {
    content: "";
    display: block;
    width: 9px;
    height: 9px;
    border-bottom: 1px solid #000;
    border-left: 1px solid #000;
    transform: rotate(-45deg);
  }
  b {
    display: none;
  }
}
.select2-container--open {
  .select2-selection__arrow {
    margin-top: -8px !important;
    &::before {
      transform: rotate(-45deg) scale(-1) !important;
    }
  }
}

.select2-container--default .select2-results__option--disabled {
  .select-option-price {
    color: #8f8f8f;
  }
}

.select2-container--default .select2-results__option--selected {
  background-color: #f5f5f5;
}

.select2-container--default .select2-results__option--highlighted.select2-results__option--selectable {
  background-color: #f5f5f5;
  color: #000;
}

.select-parent {
  position: relative;
  &__label {
    position: relative;
    margin-bottom: 8px;
    display: flex;
    align-items: center;
    gap: 8px;
    color: #454545;
    line-height: 16px;
    .select-parent--inner-label & {
      position: absolute;
      display: block;
      top: 6px;
      left: 16px;
      right: 16px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      z-index: 1;
      pointer-events: none;
      color: #8F8F8F;
      font-size: 12px;
    }
  }
}

.select-option {
  display: flex;
  align-items: center;
  justify-content: space-between;
  .select2-results & {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
  }
}

.select-option-icon {
  position: relative;
  flex: 0 0 24px;
  width: 24px;
  height: 24px;
  margin-right: 8px;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.select-option-text {
  display: block;
  flex: 1 1 auto;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: auto;
  .select-parent--inner-label & {
    padding-top: 16px;
    line-height: 30px;
  }
}

.select-option-price {
  flex: 0 0 auto;
  .select2-selection__rendered & {
    color: #ff3d00;
  }
  .select2-results__option & {
    font-weight: 500;
  }
}
