.cart {
  position: relative;
  padding-top: 20px;
  &__empty {
    position: relative;
    padding: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 16px;
    text-align: center;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
    @include response(1200px) {
      padding-left: 0;
      padding-right: 0;
    }
  }
  &__box {
    position: relative;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 60px;
    @include response(1200px) {
      display: block;
      margin-bottom: 0;
    }
  }
  &__left {
    position: relative;
    flex: 0 0 840px;
    max-width: 840px;
    width: 100%;
    @include response(1200px) {
      max-width: 100%;
    }
  }
  &__title {
    margin-bottom: 16px;
  }
  &__list {
    position: relative;
  }
  &__item {
    position: relative;
    padding-top: 24px;
    padding-bottom: 24px;
    border-bottom: 1px solid #eaeaea;
    display: flex;
    align-items: center;
    gap: 32px;
    @include response(1200px) {
      padding-top: 16px;
      padding-bottom: 16px;
      gap: 8px;
      flex-wrap: wrap;
    }
    &:first-child {
      border-top: 1px solid #eaeaea;
    }
    &:last-child {
      @include response(1200px) {
        border-bottom: 0;
      }
    }
  }
  &__item-info {
    position: relative;
    display: flex;
    align-items: center;
    gap: 16px;
    flex: 1 1 auto;
    @include response(1200px) {
      width: 100%;
    }
  }
  &__item-img {
    position: relative;
    flex: 0 0 100px;
    width: 100px;
    height: 100px;
    overflow: hidden;
    border: 1px solid transparent;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: $red;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__item-box {
    position: relative;
  }
  &__item-category {
    position: relative;
    margin-bottom: 8px;
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    @include response(1200px) {
      margin-bottom: 4px;
      padding-right: 20px;
    }
  }
  &__item-remove {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    gap: 8px;
    margin-top: 12px;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      position: absolute;
      top: 0;
      right: -10px;
      font-size: 0;
      padding: 16px 10px 5px;
      gap: 0;
      margin-top: 0;
      z-index: 1;
    }
    @include desktop-hover() {
      color: $red;
      svg {
        stroke: $red;
      }
    }
    svg {
      stroke: #4d6562;
      transition: 0.3s ease-in-out;
    }
  }
  &__item-name {
    display: block;
    text-decoration: none;
    color: #000;
    position: relative;
    margin-bottom: 8px;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 4px;
    }
    @include desktop-hover() {
      color: $red;
    }
  }
  &__item-labels {
    position: relative;
    color: #595959;
    line-height: 20px;
  }
  &__item-footer {
    flex: 0 0 120px;
    width: 120px;
    @include response(1200px) {
      margin-left: 116px;
      flex: 0 0 100px;
      width: 100px;
    }
  }
  &__item-input {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__item-price {
    position: relative;
    flex: 0 0 160px;
    width: 160px;
    text-align: right;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
    gap: 4px;
    @include response(1200px) {
      flex: 0 0 calc(100% - 224px);
      width: calc(100% - 224px);
    }
  }
  &__item-price-regular {
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__item-price-new {
    color: #f70808;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    width: 100%;
  }
  &__item-price-old {
    color: #8f8f8f;
    font-weight: 500;
    text-decoration: line-through;
    order: 3;
  }
  &__item-price-save {
    color: #f70808;
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    border-radius: 4px;
    background: #ffe4e4;
    padding: 2px 4px;
  }
  &__item-configuration {
    position: relative;
    display: inline-block;
    vertical-align: top;
    cursor: pointer;
    text-decoration: underline;
    color: var(--Primary-Accent, #ff3d00);
    line-height: 20px;
    text-underline-offset: 3px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: rgba(255, 61, 0, 0.8);
    }
  }
  &__right {
    position: sticky;
    top: 10px;
    flex: 0 0 400px;
    max-width: 400px;
    width: 100%;
    @include response(1200px) {
      position: relative;
      top: auto;
      margin-left: -16px;
      margin-right: -16px;
      max-width: calc(100% + 32px);
      width: calc(100% + 32px);
    }
  }
  &__price {
    position: relative;
    border-radius: 8px;
    border: 1px solid var(--Line-Light, #eaeaea);
    background: var(--Bg-Light-gray, #f5f5f5);
    padding: 16px 20px;
    margin-bottom: 20px;
    @include response(1200px) {
      border-radius: 0;
      border: 0;
      margin-bottom: 4px;
      padding-top: 16px;
      padding-bottom: 16px;
    }
  }
  &__price-row {
    position: relative;
    margin-bottom: 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    line-height: 24px;
  }
  &__price-title {
    position: relative;
    max-width: 50%;
  }
  &__price-value {
    position: relative;
    max-width: 50%;
    text-align: right;
    font-size: 16px;
    font-weight: 500;
    &--red {
      color: $red;
    }
  }
  &__price-total {
    position: relative;
    border-top: 1px solid #d3d3d3;
    padding-top: 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 16px;
  }
  &__price-total-title {
    font-size: 18px;
    font-weight: 500;
    max-width: 50%;
  }
  &__price-total-value {
    text-align: right;
    font-size: 22px;
    font-weight: 500;
    max-width: 50%;
  }
  &__price-btn {
    width: 100%;
  }
  &__payments {
    padding-top: 16px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 8px;
  }
  &__payments-item {
    position: relative;
    display: flex;
  }
  &__promo {
    position: relative;
    border-radius: 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    padding: 16px 20px;
  }
  &__promo-expired {
    color: var(--Secondary-Red, #f70808);
    text-align: center;
    line-height: 20px;
    margin-bottom: 16px;
    padding: 4px 12px;
    background: #ffe6e6;
  }
  &__promo-title {
    position: relative;
    margin-bottom: 16px;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__promo-form {
    position: relative;
    display: flex;
    gap: 12px;
    input {
      flex: 1 1 auto;
    }
  }
  &__promo-btn {
    flex: 0 0 auto;
  }
  &__promo-value {
    position: relative;
    margin-top: 16px;
    display: inline-flex;
    align-items: center;
    gap: 4px;
    vertical-align: top;
    border-radius: 52px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: var(--Primary-White, #fff);
    padding: 7px 11px;
    line-height: 20px;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: $red;
      svg {
        stroke: $red;
      }
    }
    span {
      color: var(--Primary-Accent, #ff3d00);
      font-weight: 500;
    }
  }
  &__promo-value-remove {
    display: flex;
    flex: 0 0 auto;
    cursor: pointer;
    svg {
      stroke: #000;
      transition: 0.3s ease-in-out;
    }
  }
}
