.blog {
  position: relative;
  padding-top: 40px;
  &__top {
    position: relative;
    display: flex;
    align-items: center;
    background: var(--Bg-Light-gray, #f5f5f5);
    border-radius: 8px;
    overflow: hidden;
    margin-bottom: 32px;
    @include response(1200px) {
      display: block;
      margin-bottom: 24px;
    }
  }
  &__top-img {
    position: relative;
    flex: 0 0 640px;
    width: 640px;
    align-self: stretch;
    @include response(1200px) {
      width: 100%;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__top-info {
    position: relative;
    padding: 32px 48px;
    @include response(1200px) {
      padding: 16px;
    }
  }
  &__top-labels {
    position: relative;
    margin: 0 0 12px;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    gap: 10px 28px;
    li {
      flex: 0 0 auto;
      position: relative;
      &:last-child {
        &::before {
          display: none;
        }
      }
      &::before {
        content: "";
        position: absolute;
        width: 4px;
        height: 4px;
        border-radius: 50%;
        background: #ff3d00;
        top: 50%;
        margin-top: -2px;
        right: -16px;
      }
    }
    a {
      display: block;
      color: var(--Primary-Accent, #ff3d00);
      font-weight: 500;
      line-height: 20px;
      text-decoration: none;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        color: $redH;
      }
    }
  }
  &__top-title {
    position: relative;
    font-family: "Instrument Sans";
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
    margin: 0 0 12px;
    @include response(1200px) {
      font-size: 18px;
      line-height: 26px;
    }
  }
  &__top-text {
    position: relative;
    overflow: hidden;
    color: var(--Main-Gray, #595959);
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 12px;
    @include response(767px) {
      display: none;
    }
    p {
      margin: 0;
    }
  }
  &__top-footer {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 16px;
  }
  &__top-author {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 12px;
  }
  &__top-author-img {
    position: relative;
    width: 32px;
    height: 32px;
    flex: 0 0 32px;
    border-radius: 50%;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__top-author-name {
    font-weight: 500;
    line-height: 20px;
    color: #000;
    text-decoration: none;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $red;
    }
  }
  &__top-date {
    color: var(--Main-Gray, #595959);
  }
  &__row {
    position: relative;
    padding-top: 32px;
    padding-bottom: 32px;
    @include response(1200px) {
      padding-top: 24px;
      padding-bottom: 24px;
    }
  }
  &__row-title {
    position: relative;
    font-family: "Instrument Sans";
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 32px;
    margin: 0 0 24px;
  }
  &__row-list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
    @include response(1200px) {
      display: block;
    }
  }
  &__item {
    flex: 0 0 calc(25% - 18px);
    max-width: calc(25% - 18px);
    width: 100%;
    border-radius: 8px;
    overflow: hidden;
    background: var(--Bg-Light-gray, #f5f5f5);
    display: flex;
    flex-direction: column;
    @include response(1200px) {
      max-width: 100%;
      margin-bottom: 24px;
      &:not(.blog__item--big) {
        flex-direction: row;
        column-gap: 12px;
        background: #fff;
        .blog__item-img {
          flex: 0 0 156px;
          width: 156px;
          border-radius: 4px;
          overflow: hidden;
        }
        .blog__item-box {
          padding: 0;
          overflow: auto;
        }
        .blog__item-author-img,
        .blog__item-date {
          display: none;
        }
        .blog__item-title {
          max-height: 48px;
          font-size: 16px;
          line-height: 24px;
          -webkit-line-clamp: 2;
          margin-bottom: 8px;
        }
        .blog__item-author-name {
          font-weight: 400;
        }
        .blog__item-labels {
          margin-bottom: 8px;
          li {
            margin-right: 20px;
            &::before {
              right: -13px;
            }
          }
          a {
            font-size: 12px;
            line-height: 16px;
          }
        }
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
    &--big {
      flex: 0 0 calc(33.333% - 16px);
      max-width: calc(33.333% - 16px);
      @include response(1200px) {
        margin-bottom: 16px;
        max-width: 100%;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
  &__item-img {
    position: relative;
    display: block;
    text-decoration: none;
    @include desktop-hover() {
      img {
        opacity: 0.9;
      }
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.3s ease-in-out;
    }
  }
  &__item-box {
    position: relative;
    padding: 16px;
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
  }
  &__item-labels {
    position: relative;
    list-style: none;
    margin: 0 0 12px;
    padding: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    li {
      display: inline-block;
      vertical-align: top;
      position: relative;
      margin-right: 28px;
      &:last-child {
        margin-right: 0;
        &::before {
          display: none;
        }
      }
      &::before {
        content: "";
        position: absolute;
        width: 4px;
        height: 4px;
        border-radius: 50%;
        background: #ff3d00;
        top: 50%;
        margin-top: -2px;
        right: -16px;
      }
    }
    a {
      display: block;
      color: var(--Primary-Accent, #ff3d00);
      font-weight: 500;
      line-height: 20px;
      text-decoration: none;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        color: $redH;
      }
    }
  }
  &__item-title {
    display: block;
    text-decoration: none;
    color: var(--Primary-Black, #000);
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    margin-bottom: 12px;
    max-height: 78px;
    overflow: hidden;
    -webkit-line-clamp: 3;
    display: box;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    flex: 1 1 auto;
    @include desktop-hover() {
      text-decoration: underline;
    }
  }
  &__item-footer {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 16px;
  }
  &__item-author {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 12px;
  }
  &__item-author-img {
    position: relative;
    width: 32px;
    height: 32px;
    flex: 0 0 32px;
    border-radius: 50%;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__item-author-name {
    font-weight: 500;
    line-height: 20px;
    color: #000;
    text-decoration: none;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $red;
    }
  }
  &__item-date {
    flex: 0 0 auto;
    color: var(--Main-Gray, #595959);
  }
  &__categories {
    position: relative;
    display: flex;
    column-gap: 20px;
    @include response(1200px) {
      column-gap: 12px;
      overflow-x: auto;
      margin-left: -16px;
      margin-right: -16px;
      padding-left: 16px;
      padding-right: 16px;
    }
  }
  &__categories-item {
    position: relative;
    width: 100%;
    text-decoration: none;
    text-align: center;
    @include response(1200px) {
      flex: 0 0 28%;
    }
    @include desktop-hover() {
      .blog__categories-item-title {
        color: $red;
      }
      .blog__categories-item-img {
        border-color: $red;
      }
    }
  }
  &__categories-item-img {
    position: relative;
    border-radius: 8px;
    border: 1px solid var(--line-light-gray-inputs-bbt, #d3d3d3);
    background: var(--Primary-White, #fff);
    overflow: hidden;
    margin-bottom: 8px;
    transition: 0.3s ease-in-out;
  }
  &__categories-item-title {
    color: #000;
    line-height: 20px;
    font-weight: 500;
    transition: 0.3s ease-in-out;
  }
  &__form {
    position: relative;
    padding: 54px 24px;
    background: var(--Secondary-Fire-Linear, linear-gradient(183deg, #01231f 54.86%, #ff3d00 90.11%));
    @include response(1200px) {
      padding-top: 32px;
      padding-bottom: 32px;
    }
    &--border-radius {
      border-radius: 8px;
      margin-bottom: 20px;
    }
  }
  &__form-box {
    position: relative;
    max-width: 580px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
  &__form-title {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 12px;
    column-gap: 16px;
    color: var(--Primary-White, #fff);
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
    svg {
      flex: 0 0 auto;
    }
  }
  &__form-text {
    position: relative;
    margin-bottom: 24px;
    color: var(--Primary-White, #fff);
    text-align: center;
    font-size: 16px;
    line-height: 24px;
    opacity: 0.7;
    @include response(1200px) {
      text-align: left;
    }
  }
  &__form-holder {
    position: relative;
    display: flex;
    column-gap: 12px;
  }
  &__form-btn {
    flex: 0 0 auto;
  }
  &__commitment {
    position: relative;
    padding-top: 64px;
    @include response(1200px) {
      padding-top: 48px;
    }
  }
  &__commitment-box {
    position: relative;
    border-radius: 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    display: flex;
    margin-bottom: 32px;
    @include response(1200px) {
      display: block;
      margin-bottom: 24px;
    }
  }
  &__commitment-left {
    position: relative;
    padding: 32px 48px;
    @include response(1200px) {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
  &__commitment-title {
    margin-bottom: 12px;
  }
  &__commitment-text {
    color: var(--Main-Gray, #595959);
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 24px;
    p {
      margin: 0 0 12px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__commitment-btn-box {
    position: relative;
  }
  &__commitment-btn {
    position: relative;
  }
  &__commitment-right {
    position: relative;
    flex: 0 0 auto;
    picture {
      display: block;
      height: 100%;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__more {
    position: relative;
    text-align: center;
    padding-top: 64px;
    @include response(1200px) {
      padding-top: 40px;
    }
  }
  &__more-info {
    margin-bottom: 16px;
    color: var(--Main-Gray, #595959);
    line-height: 20px;
  }
  &__more-btn {
    position: relative;
  }
}
