.post {
  position: relative;
  &__box {
    position: relative;
    display: flex;
    align-items: flex-start;
    column-gap: 40px;
    @include response(1200px) {
      display: block;
    }
  }
  &__nav {
    position: sticky;
    top: 10px;
    flex: 0 0 220px;
    width: 220px;
    padding: 4px;
    border-radius: 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    @include response(1200px) {
      display: none;
    }
  }
  &__nav-title {
    position: relative;
    padding: 8px 12px;
    margin-bottom: 8px;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    border-bottom: 1px solid #eaeaea;
  }
  &__nav-list {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      position: relative;
      > div {
        position: relative;
        padding: 8px 12px;
        border-radius: 4px;
        line-height: 20px;
        cursor: pointer;
        transition: 0.3s ease-in-out;
        @include desktop-hover() {
          background: #fff;
        }
        &.active {
          border-left: 2px solid var(--Primary-Accent, #ff3d00);
          background: var(--Primary-White, #fff);
          box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.04);
          color: #ff3d00;
        }
      }
    }
  }
  &__content {
    position: relative;
    flex: 0 0 700px;
    width: 700px;
    @include response(1200px) {
      width: 100%;
    }
  }
  &__title {
    margin-bottom: 16px;
  }
  &__right {
    position: sticky;
    top: 10px;
    flex: 0 0 280px;
    width: 100%;
    @include response(1200px) {
      display: none;
    }
  }
  &__form {
    position: relative;
    padding: 16px;
    border-radius: 8px;
    background: var(--Bg-Light-gray, #f5f5f5);
    margin-bottom: 24px;
  }
  &__form-head {
    position: relative;
    margin-bottom: 16px;
    display: flex;
    align-items: center;
    column-gap: 10px;
    svg {
      flex: 0 0 auto;
    }
  }
  &__form-title {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__form-btn {
    width: 100%;
  }
  &__related {
    position: relative;
  }
  &__related-title {
    position: relative;
    margin-bottom: 16px;
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
  }
  &__related-list {
    position: relative;
  }
  &__related-item {
    display: block;
    position: relative;
    margin-bottom: 16px;
    text-decoration: none;
    border-radius: 8px;
    overflow: hidden;
    @include desktop-hover() {
      .post__related-item-title {
        color: $red;
      }
    }
    &::before {
      content: "";
      position: absolute;
      background: var(--Mask-Gradient, linear-gradient(180deg, rgba(1, 35, 31, 0) 0%, rgba(1, 35, 31, 0.8) 100%));
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
    }
    img {
      width: 100%;
    }
  }
  &__related-item-title-box {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 16px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  &__related-item-title {
    color: var(--Primary-White, #fff);
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    max-height: 48px;
    overflow: hidden;
    -webkit-line-clamp: 2;
    display: box;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    transition: .3s ease-in-out;
  }
  &__create-info {
    position: relative;
    display: flex;
    padding-bottom: 16px;
    margin-bottom: 16px;
    border-bottom: 1px solid #eaeaea;
    column-gap: 12px;
    @include response(1200px) {
      display: block;
    }
  }
  &__create-item {
    position: relative;
    width: 100%;
    @include response(1200px) {
      display: flex;
      align-items: center;
      column-gap: 8px;
      margin-bottom: 12px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__create-item-head {
    position: relative;
    margin-bottom: 2px;
    display: flex;
    align-items: center;
    column-gap: 2px;
    color: var(--Main-Gray, #595959);
    line-height: 20px;
    @include response(1200px) {
      margin-bottom: 0;
    }
    svg {
      flex: 0 0 16px;
      width: 16px;
    }
  }
  &__create-item-author {
    position: relative;
    @include response(1200px) {
      position: initial;
    }
    &.active {
      .post__create-item-author-title:not(.post__create-item-author-title--no-hint) {
        color: #ff3d00;
        border-color: #ff3d00;
      }
      .post__create-item-hint {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
      }
    }
    @include desktop-hover() {
      .post__create-item-author-title:not(.post__create-item-author-title--no-hint) {
        color: #ff3d00;
        border-color: #ff3d00;
      }
      .post__create-item-hint {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
      }
    }
  }
  &__create-item-author-title {
    display: inline-block;
    vertical-align: top;
    color: var(--Primary-Black, #000);
    font-weight: 500;
    line-height: 20px;
    border-bottom: 1px dashed #4d6562;
    transition: 0.3s ease-in-out;
    cursor: pointer;
    &--no-hint {
      border-bottom: 0;
      cursor: initial;
    }
  }
  &__create-item-hint {
    position: absolute;
    top: calc(100% + 10px);
    left: 0;
    border-radius: 8px;
    border: 1px solid var(--Line-Light, #eaeaea);
    background: var(--Primary-White, #fff);
    box-shadow: 0px 4px 40px 0px rgba(0, 0, 0, 0.1);
    width: 420px;
    padding: 16px;
    opacity: 0;
    visibility: hidden;
    transform: translateY(20px);
    transition: 0.2s ease-in-out;
    z-index: 1;
    @include response(1200px) {
      width: calc(100vw - 32px);
    }
  }
  &__create-item-hint-body {
    position: relative;
    display: flex;
    column-gap: 12px;
    border-bottom: 1px solid #eaeaea;
    padding-bottom: 12px;
    margin-bottom: 12px;
  }
  &__create-item-ava {
    position: relative;
    flex: 0 0 60px;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__create-item-hint-holder {
    position: relative;
  }
  &__create-item-hint-title {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    margin-bottom: 8px;
  }
  &__create-item-hint-text {
    line-height: 20px;
    margin-bottom: 8px;
    p {
      margin: 0;
    }
  }
  &__create-item-hint-btn {
    display: inline-flex;
    align-items: center;
    vertical-align: top;
    column-gap: 8px;
    color: var(--Primary-Accent, #ff3d00);
    font-weight: 500;
    line-height: 20px;
    text-decoration: none;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $redH;
      svg {
        stroke: $redH;
      }
    }
    svg {
      stroke: #ff3d00;
    }
  }
  &__create-item-hint-footer {
    position: relative;
    color: var(--Main-Gray, #595959);
    font-weight: 400;
    line-height: 20px;
    span {
      color: var(--Primary-Black, #000);
      font-weight: 500;
    }
  }
  &__text-content {
    position: relative;
  }
  &__faq {
    position: relative;
  }
  &__faq-title {
    position: relative;
    margin: 0 0 24px;
    color: var(--Primary-Black, #000);
    font-family: "Instrument Sans";
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 32px;
  }
}
