.triggers {
  position: relative;
  background: var(--main-70, #4d6562);
  padding-top: 24px;
  padding-bottom: 24px;
  margin-bottom: -40px;
  margin-top: 40px;
  @include response(1200px) {
    margin-bottom: -20px;
    margin-top: 20px;
    padding-top: 16px;
    padding-bottom: 16px;
  }
  &__list {
    position: relative;
    display: flex;
    justify-content: center;
    column-gap: 96px;
    @include response(1200px) {
      column-gap: 24px;
    }
  }
  &__item {
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 12px;
    color: var(--Primary-White, #fff);
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    @include response(1200px) {
      font-size: 14px;
      line-height: 20px;
      flex-direction: column;
      text-align: center;
      row-gap: 12px;
      > img {
        width: 24px;
        height: 24px;
      }
    }
    &:last-child {
      &::before {
        display: none;
      }
    }
    &::before {
      content: "";
      position: absolute;
      width: 1px;
      top: 0;
      bottom: 0;
      background: rgba(255, 255, 255, 0.16);
      right: -48px;
      @include response(1200px) {
        right: -12px;
      }
    }
  }
}
